module.exports = [{"name":"Evented","namespace":"Evented","description":"Methods mixed in to other classes for event capabilities.\n","path":"/maplibre-gl-js-docs/api/events/#evented"},{"name":"off","namespace":"Evented#off","description":"Removes a previously registered event listener.\n","path":"/maplibre-gl-js-docs/api/events/#evented#off"},{"name":"on","namespace":"Evented#on","description":"Adds a listener to a specified event type.\n","path":"/maplibre-gl-js-docs/api/events/#evented#on"},{"name":"once","namespace":"Evented#once","description":"Adds a listener that will be called only once to a specified event type.\n\nThe listener will be called first time the event fires after the listener is registered.\n","path":"/maplibre-gl-js-docs/api/events/#evented#once"},{"name":"MapDataEvent","namespace":"MapDataEvent","description":"A `MapDataEvent` object is emitted with the <Map.event:data>\nand <Map.event:dataloading> events. Possible values for\n`dataType`s are:\n\n-   `'source'`: The non-tile data associated with any source\n-   `'style'`: The [style](https://maplibre.org/maplibre-gl-js-docs/style-spec/) used by the map\n","path":"/maplibre-gl-js-docs/api/events/#mapdataevent"},{"name":"MapLibreZoomEvent","namespace":"MapLibreZoomEvent","description":"A `MapLibreZoomEvent` is the event type for the boxzoom-related map events emitted by the [BoxZoomHandler](BoxZoomHandler).\n","path":"/maplibre-gl-js-docs/api/events/#maplibrezoomevent"},{"name":"MapMouseEvent","namespace":"MapMouseEvent","description":"`MapMouseEvent` is the event type for mouse-related map events.\n","path":"/maplibre-gl-js-docs/api/events/#mapmouseevent"},{"name":"lngLat","namespace":"MapMouseEvent#lngLat","description":"The geographic location on the map of the mouse cursor.\n","path":"/maplibre-gl-js-docs/api/events/#mapmouseevent#lnglat"},{"name":"originalEvent","namespace":"MapMouseEvent#originalEvent","description":"The DOM event which caused the map event.\n","path":"/maplibre-gl-js-docs/api/events/#mapmouseevent#originalevent"},{"name":"point","namespace":"MapMouseEvent#point","description":"The pixel coordinates of the mouse cursor, relative to the map and measured from the top left corner.\n","path":"/maplibre-gl-js-docs/api/events/#mapmouseevent#point"},{"name":"preventDefault","namespace":"MapMouseEvent#preventDefault","description":"Prevents subsequent default processing of the event by the map.\n\nCalling this method will prevent the following default map behaviors:\n\n-   On `mousedown` events, the behavior of [DragPanHandler](DragPanHandler)\n-   On `mousedown` events, the behavior of [DragRotateHandler](DragRotateHandler)\n-   On `mousedown` events, the behavior of [BoxZoomHandler](BoxZoomHandler)\n-   On `dblclick` events, the behavior of [DoubleClickZoomHandler](DoubleClickZoomHandler)\n","path":"/maplibre-gl-js-docs/api/events/#mapmouseevent#preventdefault"},{"name":"target","namespace":"MapMouseEvent#target","description":"The `Map` object that fired the event.\n","path":"/maplibre-gl-js-docs/api/events/#mapmouseevent#target"},{"name":"type","namespace":"MapMouseEvent#type","description":"The event type (one of <Map.event:mousedown>,\n<Map.event:mouseup>,\n<Map.event:click>,\n<Map.event:dblclick>,\n<Map.event:mousemove>,\n<Map.event:mouseover>,\n<Map.event:mouseenter>,\n<Map.event:mouseleave>,\n<Map.event:mouseout>,\n<Map.event:contextmenu>).\n","path":"/maplibre-gl-js-docs/api/events/#mapmouseevent#type"},{"name":"MapTouchEvent","namespace":"MapTouchEvent","description":"`MapTouchEvent` is the event type for touch-related map events.\n","path":"/maplibre-gl-js-docs/api/events/#maptouchevent"},{"name":"lngLat","namespace":"MapTouchEvent#lngLat","description":"The geographic location on the map of the center of the touch event points.\n","path":"/maplibre-gl-js-docs/api/events/#maptouchevent#lnglat"},{"name":"lngLats","namespace":"MapTouchEvent#lngLats","description":"The geographical locations on the map corresponding to a\n[touch event's `touches`](https://developer.mozilla.org/en-US/docs/Web/API/TouchEvent/touches) property.\n","path":"/maplibre-gl-js-docs/api/events/#maptouchevent#lnglats"},{"name":"originalEvent","namespace":"MapTouchEvent#originalEvent","description":"The DOM event which caused the map event.\n","path":"/maplibre-gl-js-docs/api/events/#maptouchevent#originalevent"},{"name":"point","namespace":"MapTouchEvent#point","description":"The pixel coordinates of the center of the touch event points, relative to the map and measured from the top left\ncorner.\n","path":"/maplibre-gl-js-docs/api/events/#maptouchevent#point"},{"name":"points","namespace":"MapTouchEvent#points","description":"The array of pixel coordinates corresponding to a\n[touch event's `touches`](https://developer.mozilla.org/en-US/docs/Web/API/TouchEvent/touches) property.\n","path":"/maplibre-gl-js-docs/api/events/#maptouchevent#points"},{"name":"preventDefault","namespace":"MapTouchEvent#preventDefault","description":"Prevents subsequent default processing of the event by the map.\n\nCalling this method will prevent the following default map behaviors:\n\n-   On `touchstart` events, the behavior of [DragPanHandler](DragPanHandler)\n-   On `touchstart` events, the behavior of [TouchZoomRotateHandler](TouchZoomRotateHandler)\n","path":"/maplibre-gl-js-docs/api/events/#maptouchevent#preventdefault"},{"name":"target","namespace":"MapTouchEvent#target","description":"The `Map` object that fired the event.\n","path":"/maplibre-gl-js-docs/api/events/#maptouchevent#target"},{"name":"type","namespace":"MapTouchEvent#type","description":"The event type.\n","path":"/maplibre-gl-js-docs/api/events/#maptouchevent#type"},{"name":"MapWheelEvent","namespace":"MapWheelEvent","description":"`MapWheelEvent` is the event type for the `wheel` map event.\n","path":"/maplibre-gl-js-docs/api/events/#mapwheelevent"},{"name":"originalEvent","namespace":"MapWheelEvent#originalEvent","description":"The DOM event which caused the map event.\n","path":"/maplibre-gl-js-docs/api/events/#mapwheelevent#originalevent"},{"name":"preventDefault","namespace":"MapWheelEvent#preventDefault","description":"Prevents subsequent default processing of the event by the map.\n\nCalling this method will prevent the the behavior of [ScrollZoomHandler](ScrollZoomHandler).\n","path":"/maplibre-gl-js-docs/api/events/#mapwheelevent#preventdefault"},{"name":"target","namespace":"MapWheelEvent#target","description":"The `Map` object that fired the event.\n","path":"/maplibre-gl-js-docs/api/events/#mapwheelevent#target"},{"name":"type","namespace":"MapWheelEvent#type","description":"The event type.\n","path":"/maplibre-gl-js-docs/api/events/#mapwheelevent#type"},{"name":"EdgeInsets","namespace":"EdgeInsets","description":"An `EdgeInset` object represents screen space padding applied to the edges of the viewport.\nThis shifts the apprent center or the vanishing point of the map. This is useful for adding floating UI elements\non top of the map and having the vanishing point shift as UI elements resize.\n","path":"/maplibre-gl-js-docs/api/geography/#edgeinsets"},{"name":"getCenter","namespace":"EdgeInsetsgetCenter","description":"Utility method that computes the new apprent center or vanishing point after applying insets.\nThis is in pixels and with the top left being (0.0) and +y being downwards.\n","path":"/maplibre-gl-js-docs/api/geography/#edgeinsetsgetcenter"},{"name":"interpolate","namespace":"EdgeInsetsinterpolate","description":"Interpolates the inset in-place.\nThis maintains the current inset value for any inset not present in `target`.\n","path":"/maplibre-gl-js-docs/api/geography/#edgeinsetsinterpolate"},{"name":"toJSON","namespace":"EdgeInsetstoJSON","description":"Returns the current state as json, useful when you want to have a\nread-only representation of the inset.\n","path":"/maplibre-gl-js-docs/api/geography/#edgeinsetstojson"},{"name":"LngLat","namespace":"LngLat","description":"A `LngLat` object represents a given longitude and latitude coordinate, measured in degrees.\nThese coordinates are based on the [WGS84 (EPSG:4326) standard](https://en.wikipedia.org/wiki/World_Geodetic_System#WGS84).\n\nMapLibre GL uses longitude, latitude coordinate order (as opposed to latitude, longitude) to match the\n[GeoJSON specification](https://tools.ietf.org/html/rfc7946).\n\nNote that any MapLibre GL method that accepts a `LngLat` object as an argument or option\ncan also accept an `Array` of two numbers and will perform an implicit conversion.\nThis flexible type is documented as [LngLatLike](LngLatLike).\n","path":"/maplibre-gl-js-docs/api/geography/#lnglat"},{"name":"convert","namespace":"LngLat.convert","description":"Converts an array of two numbers or an object with `lng` and `lat` or `lon` and `lat` properties\nto a `LngLat` object.\n\nIf a `LngLat` object is passed in, the function returns it unchanged.\n","path":"/maplibre-gl-js-docs/api/geography/#lnglat.convert"},{"name":"distanceTo","namespace":"LngLat#distanceTo","description":"Returns the approximate distance between a pair of coordinates in meters\nUses the Haversine Formula (from R.W. Sinnott, \"Virtues of the Haversine\", Sky and Telescope, vol. 68, no. 2, 1984, p. 159)\n","path":"/maplibre-gl-js-docs/api/geography/#lnglat#distanceto"},{"name":"toArray","namespace":"LngLat#toArray","description":"Returns the coordinates represented as an array of two numbers.\n","path":"/maplibre-gl-js-docs/api/geography/#lnglat#toarray"},{"name":"toBounds","namespace":"LngLat#toBounds","description":"Returns a `LngLatBounds` from the coordinates extended by a given `radius`. The returned `LngLatBounds` completely contains the `radius`.\n","path":"/maplibre-gl-js-docs/api/geography/#lnglat#tobounds"},{"name":"toString","namespace":"LngLat#toString","description":"Returns the coordinates represent as a string.\n","path":"/maplibre-gl-js-docs/api/geography/#lnglat#tostring"},{"name":"wrap","namespace":"LngLat#wrap","description":"Returns a new `LngLat` object whose longitude is wrapped to the range (-180, 180).\n","path":"/maplibre-gl-js-docs/api/geography/#lnglat#wrap"},{"name":"LngLatBounds","namespace":"LngLatBounds","description":"A `LngLatBounds` object represents a geographical bounding box,\ndefined by its southwest and northeast points in longitude and latitude.\n\nIf no arguments are provided to the constructor, a `null` bounding box is created.\n\nNote that any Mapbox GL method that accepts a `LngLatBounds` object as an argument or option\ncan also accept an `Array` of two [LngLatLike](LngLatLike) constructs and will perform an implicit conversion.\nThis flexible type is documented as [LngLatBoundsLike](LngLatBoundsLike).\n","path":"/maplibre-gl-js-docs/api/geography/#lnglatbounds"},{"name":"convert","namespace":"LngLatBounds.convert","description":"Converts an array to a `LngLatBounds` object.\n\nIf a `LngLatBounds` object is passed in, the function returns it unchanged.\n\nInternally, the function calls `LngLat#convert` to convert arrays to `LngLat` values.\n","path":"/maplibre-gl-js-docs/api/geography/#lnglatbounds.convert"},{"name":"contains","namespace":"LngLatBounds#contains","description":"Check if the point is within the bounding box.\n","path":"/maplibre-gl-js-docs/api/geography/#lnglatbounds#contains"},{"name":"extend","namespace":"LngLatBounds#extend","description":"Extend the bounds to include a given LngLatLike or LngLatBoundsLike.\n","path":"/maplibre-gl-js-docs/api/geography/#lnglatbounds#extend"},{"name":"getCenter","namespace":"LngLatBounds#getCenter","description":"Returns the geographical coordinate equidistant from the bounding box's corners.\n","path":"/maplibre-gl-js-docs/api/geography/#lnglatbounds#getcenter"},{"name":"getEast","namespace":"LngLatBounds#getEast","description":"Returns the east edge of the bounding box.\n","path":"/maplibre-gl-js-docs/api/geography/#lnglatbounds#geteast"},{"name":"getNorth","namespace":"LngLatBounds#getNorth","description":"Returns the north edge of the bounding box.\n","path":"/maplibre-gl-js-docs/api/geography/#lnglatbounds#getnorth"},{"name":"getNorthEast","namespace":"LngLatBounds#getNorthEast","description":"Returns the northeast corner of the bounding box.\n","path":"/maplibre-gl-js-docs/api/geography/#lnglatbounds#getnortheast"},{"name":"getNorthWest","namespace":"LngLatBounds#getNorthWest","description":"Returns the northwest corner of the bounding box.\n","path":"/maplibre-gl-js-docs/api/geography/#lnglatbounds#getnorthwest"},{"name":"getSouth","namespace":"LngLatBounds#getSouth","description":"Returns the south edge of the bounding box.\n","path":"/maplibre-gl-js-docs/api/geography/#lnglatbounds#getsouth"},{"name":"getSouthEast","namespace":"LngLatBounds#getSouthEast","description":"Returns the southeast corner of the bounding box.\n","path":"/maplibre-gl-js-docs/api/geography/#lnglatbounds#getsoutheast"},{"name":"getSouthWest","namespace":"LngLatBounds#getSouthWest","description":"Returns the southwest corner of the bounding box.\n","path":"/maplibre-gl-js-docs/api/geography/#lnglatbounds#getsouthwest"},{"name":"getWest","namespace":"LngLatBounds#getWest","description":"Returns the west edge of the bounding box.\n","path":"/maplibre-gl-js-docs/api/geography/#lnglatbounds#getwest"},{"name":"isEmpty","namespace":"LngLatBounds#isEmpty","description":"Check if the bounding box is an empty/`null`-type box.\n","path":"/maplibre-gl-js-docs/api/geography/#lnglatbounds#isempty"},{"name":"setNorthEast","namespace":"LngLatBounds#setNorthEast","description":"Set the northeast corner of the bounding box\n","path":"/maplibre-gl-js-docs/api/geography/#lnglatbounds#setnortheast"},{"name":"setSouthWest","namespace":"LngLatBounds#setSouthWest","description":"Set the southwest corner of the bounding box\n","path":"/maplibre-gl-js-docs/api/geography/#lnglatbounds#setsouthwest"},{"name":"toArray","namespace":"LngLatBounds#toArray","description":"Returns the bounding box represented as an array.\n","path":"/maplibre-gl-js-docs/api/geography/#lnglatbounds#toarray"},{"name":"toString","namespace":"LngLatBounds#toString","description":"Return the bounding box represented as a string.\n","path":"/maplibre-gl-js-docs/api/geography/#lnglatbounds#tostring"},{"name":"LngLatBoundsLike","namespace":"LngLatBoundsLike","description":"A [LngLatBounds](LngLatBounds) object, an array of [LngLatLike](LngLatLike) objects in \\[sw, ne] order,\nor an array of numbers in \\[west, south, east, north] order.\n","path":"/maplibre-gl-js-docs/api/geography/#lnglatboundslike"},{"name":"LngLatLike","namespace":"LngLatLike","description":"A [LngLat](LngLat) object, an array of two numbers representing longitude and latitude,\nor an object with `lng` and `lat` or `lon` and `lat` properties.\n","path":"/maplibre-gl-js-docs/api/geography/#lnglatlike"},{"name":"MercatorCoordinate","namespace":"MercatorCoordinate","description":"A `MercatorCoordinate` object represents a projected three dimensional position.\n\n`MercatorCoordinate` uses the web mercator projection ([EPSG:3857](https://epsg.io/3857)) with slightly different units:\n\n-   the size of 1 unit is the width of the projected world instead of the \"mercator meter\"\n-   the origin of the coordinate space is at the north-west corner instead of the middle\n\nFor example, `MercatorCoordinate(0, 0, 0)` is the north-west corner of the mercator world and\n`MercatorCoordinate(1, 1, 0)` is the south-east corner. If you are familiar with\n[vector tiles](https://github.com/mapbox/vector-tile-spec) it may be helpful to think\nof the coordinate space as the `0/0/0` tile with an extent of `1`.\n\nThe `z` dimension of `MercatorCoordinate` is conformal. A cube in the mercator coordinate space would be rendered as a cube.\n","path":"/maplibre-gl-js-docs/api/geography/#mercatorcoordinate"},{"name":"fromLngLat","namespace":"MercatorCoordinate.fromLngLat","description":"Project a `LngLat` to a `MercatorCoordinate`.\n","path":"/maplibre-gl-js-docs/api/geography/#mercatorcoordinate.fromlnglat"},{"name":"meterInMercatorCoordinateUnits","namespace":"MercatorCoordinate#meterInMercatorCoordinateUnits","description":"Returns the distance of 1 meter in `MercatorCoordinate` units at this latitude.\n\nFor coordinates in real world units using meters, this naturally provides the scale\nto transform into `MercatorCoordinate`s.\n","path":"/maplibre-gl-js-docs/api/geography/#mercatorcoordinate#meterinmercatorcoordinateunits"},{"name":"toAltitude","namespace":"MercatorCoordinate#toAltitude","description":"Returns the altitude in meters of the coordinate.\n","path":"/maplibre-gl-js-docs/api/geography/#mercatorcoordinate#toaltitude"},{"name":"toLngLat","namespace":"MercatorCoordinate#toLngLat","description":"Returns the `LngLat` for the coordinate.\n","path":"/maplibre-gl-js-docs/api/geography/#mercatorcoordinate#tolnglat"},{"name":"PointLike","namespace":"PointLike","description":"A [Point](https://github.com/mapbox/point-geometry) or an array of two numbers representing `x` and `y` screen coordinates in pixels.\n","path":"/maplibre-gl-js-docs/api/geography/#pointlike"},{"name":"BoxZoomHandler","namespace":"BoxZoomHandler","description":"The `BoxZoomHandler` allows the user to zoom the map to fit within a bounding box.\nThe bounding box is defined by clicking and holding `shift` while dragging the cursor.\n","path":"/maplibre-gl-js-docs/api/handlers/#boxzoomhandler"},{"name":"disable","namespace":"BoxZoomHandler#disable","description":"Disables the \"box zoom\" interaction.\n","path":"/maplibre-gl-js-docs/api/handlers/#boxzoomhandler#disable"},{"name":"enable","namespace":"BoxZoomHandler#enable","description":"Enables the \"box zoom\" interaction.\n","path":"/maplibre-gl-js-docs/api/handlers/#boxzoomhandler#enable"},{"name":"isActive","namespace":"BoxZoomHandler#isActive","description":"Returns a Boolean indicating whether the \"box zoom\" interaction is active, i.e. currently being used.\n","path":"/maplibre-gl-js-docs/api/handlers/#boxzoomhandler#isactive"},{"name":"isEnabled","namespace":"BoxZoomHandler#isEnabled","description":"Returns a Boolean indicating whether the \"box zoom\" interaction is enabled.\n","path":"/maplibre-gl-js-docs/api/handlers/#boxzoomhandler#isenabled"},{"name":"DoubleClickZoomHandler","namespace":"DoubleClickZoomHandler","description":"The `DoubleClickZoomHandler` allows the user to zoom the map at a point by\ndouble clicking or double tapping.\n","path":"/maplibre-gl-js-docs/api/handlers/#doubleclickzoomhandler"},{"name":"disable","namespace":"DoubleClickZoomHandler#disable","description":"Disables the \"double click to zoom\" interaction.\n","path":"/maplibre-gl-js-docs/api/handlers/#doubleclickzoomhandler#disable"},{"name":"enable","namespace":"DoubleClickZoomHandler#enable","description":"Enables the \"double click to zoom\" interaction.\n","path":"/maplibre-gl-js-docs/api/handlers/#doubleclickzoomhandler#enable"},{"name":"isActive","namespace":"DoubleClickZoomHandler#isActive","description":"Returns a Boolean indicating whether the \"double click to zoom\" interaction is active, i.e. currently being used.\n","path":"/maplibre-gl-js-docs/api/handlers/#doubleclickzoomhandler#isactive"},{"name":"isEnabled","namespace":"DoubleClickZoomHandler#isEnabled","description":"Returns a Boolean indicating whether the \"double click to zoom\" interaction is enabled.\n","path":"/maplibre-gl-js-docs/api/handlers/#doubleclickzoomhandler#isenabled"},{"name":"DragPanHandler","namespace":"DragPanHandler","description":"The `DragPanHandler` allows the user to pan the map by clicking and dragging\nthe cursor.\n","path":"/maplibre-gl-js-docs/api/handlers/#dragpanhandler"},{"name":"disable","namespace":"DragPanHandler#disable","description":"Disables the \"drag to pan\" interaction.\n","path":"/maplibre-gl-js-docs/api/handlers/#dragpanhandler#disable"},{"name":"enable","namespace":"DragPanHandler#enable","description":"Enables the \"drag to pan\" interaction.\n","path":"/maplibre-gl-js-docs/api/handlers/#dragpanhandler#enable"},{"name":"isActive","namespace":"DragPanHandler#isActive","description":"Returns a Boolean indicating whether the \"drag to pan\" interaction is active, i.e. currently being used.\n","path":"/maplibre-gl-js-docs/api/handlers/#dragpanhandler#isactive"},{"name":"isEnabled","namespace":"DragPanHandler#isEnabled","description":"Returns a Boolean indicating whether the \"drag to pan\" interaction is enabled.\n","path":"/maplibre-gl-js-docs/api/handlers/#dragpanhandler#isenabled"},{"name":"DragRotateHandler","namespace":"DragRotateHandler","description":"The `DragRotateHandler` allows the user to rotate the map by clicking and\ndragging the cursor while holding the right mouse button or `ctrl` key.\n","path":"/maplibre-gl-js-docs/api/handlers/#dragrotatehandler"},{"name":"disable","namespace":"DragRotateHandler#disable","description":"Disables the \"drag to rotate\" interaction.\n","path":"/maplibre-gl-js-docs/api/handlers/#dragrotatehandler#disable"},{"name":"enable","namespace":"DragRotateHandler#enable","description":"Enables the \"drag to rotate\" interaction.\n","path":"/maplibre-gl-js-docs/api/handlers/#dragrotatehandler#enable"},{"name":"isActive","namespace":"DragRotateHandler#isActive","description":"Returns a Boolean indicating whether the \"drag to rotate\" interaction is active, i.e. currently being used.\n","path":"/maplibre-gl-js-docs/api/handlers/#dragrotatehandler#isactive"},{"name":"isEnabled","namespace":"DragRotateHandler#isEnabled","description":"Returns a Boolean indicating whether the \"drag to rotate\" interaction is enabled.\n","path":"/maplibre-gl-js-docs/api/handlers/#dragrotatehandler#isenabled"},{"name":"KeyboardHandler","namespace":"KeyboardHandler","description":"The `KeyboardHandler` allows the user to zoom, rotate, and pan the map using\nthe following keyboard shortcuts:\n\n-   `=` / `+`: Increase the zoom level by 1.\n-   `Shift-=` / `Shift-+`: Increase the zoom level by 2.\n-   `-`: Decrease the zoom level by 1.\n-   `Shift--`: Decrease the zoom level by 2.\n-   Arrow keys: Pan by 100 pixels.\n-   `Shift+⇢`: Increase the rotation by 15 degrees.\n-   `Shift+⇠`: Decrease the rotation by 15 degrees.\n-   `Shift+⇡`: Increase the pitch by 10 degrees.\n-   `Shift+⇣`: Decrease the pitch by 10 degrees.\n","path":"/maplibre-gl-js-docs/api/handlers/#keyboardhandler"},{"name":"disable","namespace":"KeyboardHandler#disable","description":"Disables the \"keyboard rotate and zoom\" interaction.\n","path":"/maplibre-gl-js-docs/api/handlers/#keyboardhandler#disable"},{"name":"disableRotation","namespace":"KeyboardHandler#disableRotation","description":"Disables the \"keyboard pan/rotate\" interaction, leaving the\n\"keyboard zoom\" interaction enabled.\n","path":"/maplibre-gl-js-docs/api/handlers/#keyboardhandler#disablerotation"},{"name":"enable","namespace":"KeyboardHandler#enable","description":"Enables the \"keyboard rotate and zoom\" interaction.\n","path":"/maplibre-gl-js-docs/api/handlers/#keyboardhandler#enable"},{"name":"enableRotation","namespace":"KeyboardHandler#enableRotation","description":"Enables the \"keyboard pan/rotate\" interaction.\n","path":"/maplibre-gl-js-docs/api/handlers/#keyboardhandler#enablerotation"},{"name":"isActive","namespace":"KeyboardHandler#isActive","description":"Returns true if the handler is enabled and has detected the start of a\nzoom/rotate gesture.\n","path":"/maplibre-gl-js-docs/api/handlers/#keyboardhandler#isactive"},{"name":"isEnabled","namespace":"KeyboardHandler#isEnabled","description":"Returns a Boolean indicating whether the \"keyboard rotate and zoom\"\ninteraction is enabled.\n","path":"/maplibre-gl-js-docs/api/handlers/#keyboardhandler#isenabled"},{"name":"ScrollZoomHandler","namespace":"ScrollZoomHandler","description":"The `ScrollZoomHandler` allows the user to zoom the map by scrolling.\n","path":"/maplibre-gl-js-docs/api/handlers/#scrollzoomhandler"},{"name":"disable","namespace":"ScrollZoomHandler#disable","description":"Disables the \"scroll to zoom\" interaction.\n","path":"/maplibre-gl-js-docs/api/handlers/#scrollzoomhandler#disable"},{"name":"enable","namespace":"ScrollZoomHandler#enable","description":"Enables the \"scroll to zoom\" interaction.\n","path":"/maplibre-gl-js-docs/api/handlers/#scrollzoomhandler#enable"},{"name":"isEnabled","namespace":"ScrollZoomHandler#isEnabled","description":"Returns a Boolean indicating whether the \"scroll to zoom\" interaction is enabled.\n","path":"/maplibre-gl-js-docs/api/handlers/#scrollzoomhandler#isenabled"},{"name":"setWheelZoomRate","namespace":"ScrollZoomHandler#setWheelZoomRate","description":"Set the zoom rate of a mouse wheel\n","path":"/maplibre-gl-js-docs/api/handlers/#scrollzoomhandler#setwheelzoomrate"},{"name":"setZoomRate","namespace":"ScrollZoomHandler#setZoomRate","description":"Set the zoom rate of a trackpad\n","path":"/maplibre-gl-js-docs/api/handlers/#scrollzoomhandler#setzoomrate"},{"name":"TouchPitchHandler","namespace":"TouchPitchHandler","description":"The `TouchPitchHandler` allows the user to pitch the map by dragging up and down with two fingers.\n","path":"/maplibre-gl-js-docs/api/handlers/#touchpitchhandler"},{"name":"disable","namespace":"TouchPitchHandler#disable","description":"Disables the \"drag to pitch\" interaction.\n","path":"/maplibre-gl-js-docs/api/handlers/#touchpitchhandler#disable"},{"name":"enable","namespace":"TouchPitchHandler#enable","description":"Enables the \"drag to pitch\" interaction.\n","path":"/maplibre-gl-js-docs/api/handlers/#touchpitchhandler#enable"},{"name":"isActive","namespace":"TouchPitchHandler#isActive","description":"Returns a Boolean indicating whether the \"drag to pitch\" interaction is active, i.e. currently being used.\n","path":"/maplibre-gl-js-docs/api/handlers/#touchpitchhandler#isactive"},{"name":"isEnabled","namespace":"TouchPitchHandler#isEnabled","description":"Returns a Boolean indicating whether the \"drag to pitch\" interaction is enabled.\n","path":"/maplibre-gl-js-docs/api/handlers/#touchpitchhandler#isenabled"},{"name":"TouchZoomRotateHandler","namespace":"TouchZoomRotateHandler","description":"The `TouchZoomRotateHandler` allows the user to zoom and rotate the map by\npinching on a touchscreen.\n\nThey can zoom with one finger by double tapping and dragging. On the second tap,\nhold the finger down and drag up or down to zoom in or out.\n","path":"/maplibre-gl-js-docs/api/handlers/#touchzoomrotatehandler"},{"name":"disable","namespace":"TouchZoomRotateHandler#disable","description":"Disables the \"pinch to rotate and zoom\" interaction.\n","path":"/maplibre-gl-js-docs/api/handlers/#touchzoomrotatehandler#disable"},{"name":"disableRotation","namespace":"TouchZoomRotateHandler#disableRotation","description":"Disables the \"pinch to rotate\" interaction, leaving the \"pinch to zoom\"\ninteraction enabled.\n","path":"/maplibre-gl-js-docs/api/handlers/#touchzoomrotatehandler#disablerotation"},{"name":"enable","namespace":"TouchZoomRotateHandler#enable","description":"Enables the \"pinch to rotate and zoom\" interaction.\n","path":"/maplibre-gl-js-docs/api/handlers/#touchzoomrotatehandler#enable"},{"name":"enableRotation","namespace":"TouchZoomRotateHandler#enableRotation","description":"Enables the \"pinch to rotate\" interaction.\n","path":"/maplibre-gl-js-docs/api/handlers/#touchzoomrotatehandler#enablerotation"},{"name":"isActive","namespace":"TouchZoomRotateHandler#isActive","description":"Returns true if the handler is enabled and has detected the start of a zoom/rotate gesture.\n","path":"/maplibre-gl-js-docs/api/handlers/#touchzoomrotatehandler#isactive"},{"name":"isEnabled","namespace":"TouchZoomRotateHandler#isEnabled","description":"Returns a Boolean indicating whether the \"pinch to rotate and zoom\" interaction is enabled.\n","path":"/maplibre-gl-js-docs/api/handlers/#touchzoomrotatehandler#isenabled"},{"name":"Map","namespace":"Map","description":"The `Map` object represents the map on your page. It exposes methods\nand properties that enable you to programmatically change the map,\nand fires events as users interact with it.\n\nYou create a `Map` by specifying a `container` and other options.\nThen MapLibre GL JS initializes the map on the page and returns your `Map`\nobject.\n","path":"/maplibre-gl-js-docs/api/map/#map"},{"name":"boxzoomcancel","namespace":"Map.event:boxzoomcancel","description":"Fired when the user cancels a \"box zoom\" interaction, or when the bounding box does not meet the minimum size threshold.\nSee [BoxZoomHandler](BoxZoomHandler).\n","path":"/maplibre-gl-js-docs/api/map/#map.event:boxzoomcancel"},{"name":"boxzoomend","namespace":"Map.event:boxzoomend","description":"Fired when a \"box zoom\" interaction ends.  See [BoxZoomHandler](BoxZoomHandler).\n","path":"/maplibre-gl-js-docs/api/map/#map.event:boxzoomend"},{"name":"boxzoomstart","namespace":"Map.event:boxzoomstart","description":"Fired when a \"box zoom\" interaction starts. See [BoxZoomHandler](BoxZoomHandler).\n","path":"/maplibre-gl-js-docs/api/map/#map.event:boxzoomstart"},{"name":"click","namespace":"Map.event:click","description":"Fired when a pointing device (usually a mouse) is pressed and released at the same point on the map.\n\n**Note:** This event is compatible with the optional `layerId` parameter.\nIf `layerId` is included as the second argument in [Map#on](Map#on), the event listener will fire only when the\npoint that is pressed and released contains a visible portion of the specifed layer.\n","path":"/maplibre-gl-js-docs/api/map/#map.event:click"},{"name":"contextmenu","namespace":"Map.event:contextmenu","description":"Fired when the right button of the mouse is clicked or the context menu key is pressed within the map.\n","path":"/maplibre-gl-js-docs/api/map/#map.event:contextmenu"},{"name":"data","namespace":"Map.event:data","description":"Fired when any map data loads or changes. See [MapDataEvent](MapDataEvent)\nfor more information.\n","path":"/maplibre-gl-js-docs/api/map/#map.event:data"},{"name":"dataloading","namespace":"Map.event:dataloading","description":"Fired when any map data (style, source, tile, etc) begins loading or\nchanging asyncronously. All `dataloading` events are followed by a `data`\nor `error` event. See [MapDataEvent](MapDataEvent) for more information.\n","path":"/maplibre-gl-js-docs/api/map/#map.event:dataloading"},{"name":"dblclick","namespace":"Map.event:dblclick","description":"Fired when a pointing device (usually a mouse) is pressed and released twice at the same point on\nthe map in rapid succession.\n\n**Note:** This event is compatible with the optional `layerId` parameter.\nIf `layerId` is included as the second argument in [Map#on](Map#on), the event listener will fire only\nwhen the point that is clicked twice contains a visible portion of the specifed layer.\n","path":"/maplibre-gl-js-docs/api/map/#map.event:dblclick"},{"name":"drag","namespace":"Map.event:drag","description":"Fired repeatedly during a \"drag to pan\" interaction. See [DragPanHandler](DragPanHandler).\n","path":"/maplibre-gl-js-docs/api/map/#map.event:drag"},{"name":"dragend","namespace":"Map.event:dragend","description":"Fired when a \"drag to pan\" interaction ends. See [DragPanHandler](DragPanHandler).\n","path":"/maplibre-gl-js-docs/api/map/#map.event:dragend"},{"name":"dragstart","namespace":"Map.event:dragstart","description":"Fired when a \"drag to pan\" interaction starts. See [DragPanHandler](DragPanHandler).\n","path":"/maplibre-gl-js-docs/api/map/#map.event:dragstart"},{"name":"error","namespace":"Map.event:error","description":"Fired when an error occurs. This is GL JS's primary error reporting\nmechanism. We use an event instead of `throw` to better accommodate\nasyncronous operations. If no listeners are bound to the `error` event, the\nerror will be printed to the console.\n","path":"/maplibre-gl-js-docs/api/map/#map.event:error"},{"name":"idle","namespace":"Map.event:idle","description":"Fired after the last frame rendered before the map enters an\n\"idle\" state:\n\n-   No camera transitions are in progress\n-   All currently requested tiles have loaded\n-   All fade/transition animations have completed\n","path":"/maplibre-gl-js-docs/api/map/#map.event:idle"},{"name":"load","namespace":"Map.event:load","description":"Fired immediately after all necessary resources have been downloaded\nand the first visually complete rendering of the map has occurred.\n","path":"/maplibre-gl-js-docs/api/map/#map.event:load"},{"name":"mousedown","namespace":"Map.event:mousedown","description":"Fired when a pointing device (usually a mouse) is pressed within the map.\n\n**Note:** This event is compatible with the optional `layerId` parameter.\nIf `layerId` is included as the second argument in [Map#on](Map#on), the event listener will fire only when the\nthe cursor is pressed while inside a visible portion of the specifed layer.\n","path":"/maplibre-gl-js-docs/api/map/#map.event:mousedown"},{"name":"mouseenter","namespace":"Map.event:mouseenter","description":"Fired when a pointing device (usually a mouse) enters a visible portion of a specified layer from\noutside that layer or outside the map canvas.\n\n**Important:** This event can only be listened for when [Map#on](Map#on) includes three arguments,\nwhere the second argument specifies the desired layer.\n","path":"/maplibre-gl-js-docs/api/map/#map.event:mouseenter"},{"name":"mouseleave","namespace":"Map.event:mouseleave","description":"Fired when a pointing device (usually a mouse) leaves a visible portion of a specified layer, or leaves\nthe map canvas.\n\n**Important:** This event can only be listened for when [Map#on](Map#on) includes three arguements,\nwhere the second argument specifies the desired layer.\n","path":"/maplibre-gl-js-docs/api/map/#map.event:mouseleave"},{"name":"mousemove","namespace":"Map.event:mousemove","description":"Fired when a pointing device (usually a mouse) is moved while the cursor is inside the map.\nAs you move the cursor across the map, the event will fire every time the cursor changes position within the map.\n\n**Note:** This event is compatible with the optional `layerId` parameter.\nIf `layerId` is included as the second argument in [Map#on](Map#on), the event listener will fire only when the\nthe cursor is inside a visible portion of the specified layer.\n","path":"/maplibre-gl-js-docs/api/map/#map.event:mousemove"},{"name":"mouseout","namespace":"Map.event:mouseout","description":"Fired when a point device (usually a mouse) leaves the map's canvas.\n","path":"/maplibre-gl-js-docs/api/map/#map.event:mouseout"},{"name":"mouseover","namespace":"Map.event:mouseover","description":"Fired when a pointing device (usually a mouse) is moved within the map.\nAs you move the cursor across a web page containing a map,\nthe event will fire each time it enters the map or any child elements.\n\n**Note:** This event is compatible with the optional `layerId` parameter.\nIf `layerId` is included as the second argument in [Map#on](Map#on), the event listener will fire only when the\nthe cursor is moved inside a visible portion of the specifed layer.\n","path":"/maplibre-gl-js-docs/api/map/#map.event:mouseover"},{"name":"mouseup","namespace":"Map.event:mouseup","description":"Fired when a pointing device (usually a mouse) is released within the map.\n\n**Note:** This event is compatible with the optional `layerId` parameter.\nIf `layerId` is included as the second argument in [Map#on](Map#on), the event listener will fire only when the\nthe cursor is released while inside a visible portion of the specifed layer.\n","path":"/maplibre-gl-js-docs/api/map/#map.event:mouseup"},{"name":"move","namespace":"Map.event:move","description":"Fired repeatedly during an animated transition from one view to\nanother, as the result of either user interaction or methods such as [Map#flyTo](Map#flyTo).\n","path":"/maplibre-gl-js-docs/api/map/#map.event:move"},{"name":"moveend","namespace":"Map.event:moveend","description":"Fired just after the map completes a transition from one\nview to another, as the result of either user interaction or methods such as [Map#jumpTo](Map#jumpTo).\n","path":"/maplibre-gl-js-docs/api/map/#map.event:moveend"},{"name":"movestart","namespace":"Map.event:movestart","description":"Fired just before the map begins a transition from one\nview to another, as the result of either user interaction or methods such as [Map#jumpTo](Map#jumpTo).\n","path":"/maplibre-gl-js-docs/api/map/#map.event:movestart"},{"name":"pitch","namespace":"Map.event:pitch","description":"Fired repeatedly during the map's pitch (tilt) animation between\none state and another as the result of either user interaction\nor methods such as [Map#flyTo](Map#flyTo).\n","path":"/maplibre-gl-js-docs/api/map/#map.event:pitch"},{"name":"pitchend","namespace":"Map.event:pitchend","description":"Fired immediately after the map's pitch (tilt) finishes changing as\nthe result of either user interaction or methods such as [Map#flyTo](Map#flyTo).\n","path":"/maplibre-gl-js-docs/api/map/#map.event:pitchend"},{"name":"pitchstart","namespace":"Map.event:pitchstart","description":"Fired whenever the map's pitch (tilt) begins a change as\nthe result of either user interaction or methods such as [Map#flyTo](Map#flyTo) .\n","path":"/maplibre-gl-js-docs/api/map/#map.event:pitchstart"},{"name":"remove","namespace":"Map.event:remove","description":"Fired immediately after the map has been removed with <Map.event:remove>.\n","path":"/maplibre-gl-js-docs/api/map/#map.event:remove"},{"name":"render","namespace":"Map.event:render","description":"Fired whenever the map is drawn to the screen, as the result of\n\n-   a change to the map's position, zoom, pitch, or bearing\n-   a change to the map's style\n-   a change to a GeoJSON source\n-   the loading of a vector tile, GeoJSON file, glyph, or sprite\n","path":"/maplibre-gl-js-docs/api/map/#map.event:render"},{"name":"resize","namespace":"Map.event:resize","description":"Fired immediately after the map has been resized.\n","path":"/maplibre-gl-js-docs/api/map/#map.event:resize"},{"name":"rotate","namespace":"Map.event:rotate","description":"Fired repeatedly during a \"drag to rotate\" interaction. See [DragRotateHandler](DragRotateHandler).\n","path":"/maplibre-gl-js-docs/api/map/#map.event:rotate"},{"name":"rotateend","namespace":"Map.event:rotateend","description":"Fired when a \"drag to rotate\" interaction ends. See [DragRotateHandler](DragRotateHandler).\n","path":"/maplibre-gl-js-docs/api/map/#map.event:rotateend"},{"name":"rotatestart","namespace":"Map.event:rotatestart","description":"Fired when a \"drag to rotate\" interaction starts. See [DragRotateHandler](DragRotateHandler).\n","path":"/maplibre-gl-js-docs/api/map/#map.event:rotatestart"},{"name":"sourcedata","namespace":"Map.event:sourcedata","description":"Fired when one of the map's sources loads or changes, including if a tile belonging\nto a source loads or changes. See [MapDataEvent](MapDataEvent) for more information.\n","path":"/maplibre-gl-js-docs/api/map/#map.event:sourcedata"},{"name":"sourcedataloading","namespace":"Map.event:sourcedataloading","description":"Fired when one of the map's sources begins loading or changing asyncronously.\nAll `sourcedataloading` events are followed by a `sourcedata` or `error` event.\nSee [MapDataEvent](MapDataEvent) for more information.\n","path":"/maplibre-gl-js-docs/api/map/#map.event:sourcedataloading"},{"name":"styledata","namespace":"Map.event:styledata","description":"Fired when the map's style loads or changes. See\n[MapDataEvent](MapDataEvent) for more information.\n","path":"/maplibre-gl-js-docs/api/map/#map.event:styledata"},{"name":"styledataloading","namespace":"Map.event:styledataloading","description":"Fired when the map's style begins loading or changing asyncronously.\nAll `styledataloading` events are followed by a `styledata`\nor `error` event. See [MapDataEvent](MapDataEvent) for more information.\n","path":"/maplibre-gl-js-docs/api/map/#map.event:styledataloading"},{"name":"styleimagemissing","namespace":"Map.event:styleimagemissing","description":"Fired when an icon or pattern needed by the style is missing. The missing image can\nbe added with [Map#addImage](Map#addImage) within this event listener callback to prevent the image from\nbeing skipped. This event can be used to dynamically generate icons and patterns.\n","path":"/maplibre-gl-js-docs/api/map/#map.event:styleimagemissing"},{"name":"touchcancel","namespace":"Map.event:touchcancel","description":"Fired when a [`touchcancel`](https://developer.mozilla.org/en-US/docs/Web/Events/touchcancel) event occurs within the map.\n","path":"/maplibre-gl-js-docs/api/map/#map.event:touchcancel"},{"name":"touchend","namespace":"Map.event:touchend","description":"Fired when a [`touchend`](https://developer.mozilla.org/en-US/docs/Web/Events/touchend) event occurs within the map.\n","path":"/maplibre-gl-js-docs/api/map/#map.event:touchend"},{"name":"touchmove","namespace":"Map.event:touchmove","description":"Fired when a [`touchmove`](https://developer.mozilla.org/en-US/docs/Web/Events/touchmove) event occurs within the map.\n","path":"/maplibre-gl-js-docs/api/map/#map.event:touchmove"},{"name":"touchstart","namespace":"Map.event:touchstart","description":"Fired when a [`touchstart`](https://developer.mozilla.org/en-US/docs/Web/Events/touchstart) event occurs within the map.\n","path":"/maplibre-gl-js-docs/api/map/#map.event:touchstart"},{"name":"webglcontextlost","namespace":"Map.event:webglcontextlost","description":"Fired when the WebGL context is lost.\n","path":"/maplibre-gl-js-docs/api/map/#map.event:webglcontextlost"},{"name":"webglcontextrestored","namespace":"Map.event:webglcontextrestored","description":"Fired when the WebGL context is restored.\n","path":"/maplibre-gl-js-docs/api/map/#map.event:webglcontextrestored"},{"name":"wheel","namespace":"Map.event:wheel","description":"Fired when a [`wheel`](https://developer.mozilla.org/en-US/docs/Web/Events/wheel) event occurs within the map.\n","path":"/maplibre-gl-js-docs/api/map/#map.event:wheel"},{"name":"zoom","namespace":"Map.event:zoom","description":"Fired repeatedly during an animated transition from one zoom level to another,\nas the result of either user interaction or methods such as [Map#flyTo](Map#flyTo).\n","path":"/maplibre-gl-js-docs/api/map/#map.event:zoom"},{"name":"zoomend","namespace":"Map.event:zoomend","description":"Fired just after the map completes a transition from one zoom level to another,\nas the result of either user interaction or methods such as [Map#flyTo](Map#flyTo).\n","path":"/maplibre-gl-js-docs/api/map/#map.event:zoomend"},{"name":"zoomstart","namespace":"Map.event:zoomstart","description":"Fired just before the map begins a transition from one zoom level to another,\nas the result of either user interaction or methods such as [Map#flyTo](Map#flyTo).\n","path":"/maplibre-gl-js-docs/api/map/#map.event:zoomstart"},{"name":"addControl","namespace":"Map#addControl","description":"Adds an [IControl](IControl) to the map, calling `control.onAdd(this)`.\n","path":"/maplibre-gl-js-docs/api/map/#map#addcontrol"},{"name":"addImage","namespace":"Map#addImage","description":"Add an image to the style. This image can be displayed on the map like any other icon in the style's\nsprite using the image's ID with\n[`icon-image`](https://maplibre.org/maplibre-gl-js-docs/style-spec/#layout-symbol-icon-image),\n[`background-pattern`](https://maplibre.org/maplibre-gl-js-docs/style-spec/#paint-background-background-pattern),\n[`fill-pattern`](https://maplibre.org/maplibre-gl-js-docs/style-spec/#paint-fill-fill-pattern),\nor [`line-pattern`](https://maplibre.org/maplibre-gl-js-docs/style-spec/#paint-line-line-pattern).\nA <Map.event:error> event will be fired if there is not enough space in the sprite to add this image.\n","path":"/maplibre-gl-js-docs/api/map/#map#addimage"},{"name":"addLayer","namespace":"Map#addLayer","description":"Adds a [MapLibre style layer](https://maplibre.org/maplibre-gl-js-docs/style-spec/#layers)\nto the map's style.\n\nA layer defines how data from a specified source will be styled. Read more about layer types\nand available paint and layout properties in the [MapLibre Style Specification](https://maplibre.org/maplibre-gl-js-docs/style-spec/#layers).\n","path":"/maplibre-gl-js-docs/api/map/#map#addlayer"},{"name":"addSource","namespace":"Map#addSource","description":"Adds a source to the map's style.\n","path":"/maplibre-gl-js-docs/api/map/#map#addsource"},{"name":"areTilesLoaded","namespace":"Map#areTilesLoaded","description":"Returns a Boolean indicating whether all tiles in the viewport from all sources on\nthe style are loaded.\n","path":"/maplibre-gl-js-docs/api/map/#map#aretilesloaded"},{"name":"boxZoom","namespace":"Map#boxZoom","description":"The map's [BoxZoomHandler](BoxZoomHandler), which implements zooming using a drag gesture with the Shift key pressed.\nFind more details and examples using `boxZoom` in the [BoxZoomHandler](BoxZoomHandler) section.\n","path":"/maplibre-gl-js-docs/api/map/#map#boxzoom"},{"name":"cameraForBounds","namespace":"Map#cameraForBounds","description":"","path":"/maplibre-gl-js-docs/api/map/#map#cameraforbounds"},{"name":"doubleClickZoom","namespace":"Map#doubleClickZoom","description":"The map's [DoubleClickZoomHandler](DoubleClickZoomHandler), which allows the user to zoom by double clicking.\nFind more details and examples using `doubleClickZoom` in the [DoubleClickZoomHandler](DoubleClickZoomHandler) section.\n","path":"/maplibre-gl-js-docs/api/map/#map#doubleclickzoom"},{"name":"dragPan","namespace":"Map#dragPan","description":"The map's [DragPanHandler](DragPanHandler), which implements dragging the map with a mouse or touch gesture.\nFind more details and examples using `dragPan` in the [DragPanHandler](DragPanHandler) section.\n","path":"/maplibre-gl-js-docs/api/map/#map#dragpan"},{"name":"dragRotate","namespace":"Map#dragRotate","description":"The map's [DragRotateHandler](DragRotateHandler), which implements rotating the map while dragging with the right\nmouse button or with the Control key pressed. Find more details and examples using `dragRotate`\nin the [DragRotateHandler](DragRotateHandler) section.\n","path":"/maplibre-gl-js-docs/api/map/#map#dragrotate"},{"name":"easeTo","namespace":"Map#easeTo","description":"Changes any combination of `center`, `zoom`, `bearing`, `pitch`, and `padding` with an animated transition\nbetween old and new values. The map will retain its current values for any\ndetails not specified in `options`.\n\nNote: The transition will happen instantly if the user has enabled\nthe `reduced motion` accesibility feature enabled in their operating system,\nunless `options` includes `essential: true`.\n","path":"/maplibre-gl-js-docs/api/map/#map#easeto"},{"name":"fitBounds","namespace":"Map#fitBounds","description":"Pans and zooms the map to contain its visible area within the specified geographical bounds.\nThis function will also reset the map's bearing to 0 if bearing is nonzero.\n","path":"/maplibre-gl-js-docs/api/map/#map#fitbounds"},{"name":"fitScreenCoordinates","namespace":"Map#fitScreenCoordinates","description":"Pans, rotates and zooms the map to to fit the box made by points p0 and p1\nonce the map is rotated to the specified bearing. To zoom without rotating,\npass in the current map bearing.\n","path":"/maplibre-gl-js-docs/api/map/#map#fitscreencoordinates"},{"name":"flyTo","namespace":"Map#flyTo","description":"Changes any combination of center, zoom, bearing, and pitch, animating the transition along a curve that\nevokes flight. The animation seamlessly incorporates zooming and panning to help\nthe user maintain her bearings even after traversing a great distance.\n\nNote: The animation will be skipped, and this will behave equivalently to `jumpTo`\nif the user has the `reduced motion` accesibility feature enabled in their operating system,\nunless 'options' includes `essential: true`.\n","path":"/maplibre-gl-js-docs/api/map/#map#flyto"},{"name":"getBearing","namespace":"Map#getBearing","description":"Returns the map's current bearing. The bearing is the compass direction that is \"up\"; for example, a bearing\nof 90° orients the map so that east is up.\n","path":"/maplibre-gl-js-docs/api/map/#map#getbearing"},{"name":"getBounds","namespace":"Map#getBounds","description":"Returns the map's geographical bounds. When the bearing or pitch is non-zero, the visible region is not\nan axis-aligned rectangle, and the result is the smallest bounds that encompasses the visible region.\n","path":"/maplibre-gl-js-docs/api/map/#map#getbounds"},{"name":"getCanvas","namespace":"Map#getCanvas","description":"Returns the map's `<canvas>` element.\n","path":"/maplibre-gl-js-docs/api/map/#map#getcanvas"},{"name":"getCanvasContainer","namespace":"Map#getCanvasContainer","description":"Returns the HTML element containing the map's `<canvas>` element.\n\nIf you want to add non-GL overlays to the map, you should append them to this element.\n\nThis is the element to which event bindings for map interactivity (such as panning and zooming) are\nattached. It will receive bubbled events from child elements such as the `<canvas>`, but not from\nmap controls.\n","path":"/maplibre-gl-js-docs/api/map/#map#getcanvascontainer"},{"name":"getCenter","namespace":"Map#getCenter","description":"Returns the map's geographical centerpoint.\n","path":"/maplibre-gl-js-docs/api/map/#map#getcenter"},{"name":"getContainer","namespace":"Map#getContainer","description":"Returns the map's containing HTML element.\n","path":"/maplibre-gl-js-docs/api/map/#map#getcontainer"},{"name":"getFeatureState","namespace":"Map#getFeatureState","description":"Gets the `state` of a feature.\nA feature's `state` is a set of user-defined key-value pairs that are assigned to a feature at runtime.\nFeatures are identified by their `feature.id` attribute, which can be any number or string.\n\n_Note: To access the values in a feature's state object for the purposes of styling the feature, use the [`feature-state` expression](https://maplibre.org/maplibre-gl-js-docs/style-spec/expressions/#feature-state)._\n","path":"/maplibre-gl-js-docs/api/map/#map#getfeaturestate"},{"name":"getFilter","namespace":"Map#getFilter","description":"Returns the filter applied to the specified style layer.\n","path":"/maplibre-gl-js-docs/api/map/#map#getfilter"},{"name":"getLayer","namespace":"Map#getLayer","description":"Returns the layer with the specified ID in the map's style.\n","path":"/maplibre-gl-js-docs/api/map/#map#getlayer"},{"name":"getLayoutProperty","namespace":"Map#getLayoutProperty","description":"Returns the value of a layout property in the specified style layer.\n","path":"/maplibre-gl-js-docs/api/map/#map#getlayoutproperty"},{"name":"getLight","namespace":"Map#getLight","description":"Returns the value of the light object.\n","path":"/maplibre-gl-js-docs/api/map/#map#getlight"},{"name":"getMaxBounds","namespace":"Map#getMaxBounds","description":"Returns the maximum geographical bounds the map is constrained to, or `null` if none set.\n","path":"/maplibre-gl-js-docs/api/map/#map#getmaxbounds"},{"name":"getMaxPitch","namespace":"Map#getMaxPitch","description":"Returns the map's maximum allowable pitch.\n","path":"/maplibre-gl-js-docs/api/map/#map#getmaxpitch"},{"name":"getMaxZoom","namespace":"Map#getMaxZoom","description":"Returns the map's maximum allowable zoom level.\n","path":"/maplibre-gl-js-docs/api/map/#map#getmaxzoom"},{"name":"getMinPitch","namespace":"Map#getMinPitch","description":"Returns the map's minimum allowable pitch.\n","path":"/maplibre-gl-js-docs/api/map/#map#getminpitch"},{"name":"getMinZoom","namespace":"Map#getMinZoom","description":"Returns the map's minimum allowable zoom level.\n","path":"/maplibre-gl-js-docs/api/map/#map#getminzoom"},{"name":"getPadding","namespace":"Map#getPadding","description":"Returns the current padding applied around the map viewport.\n","path":"/maplibre-gl-js-docs/api/map/#map#getpadding"},{"name":"getPaintProperty","namespace":"Map#getPaintProperty","description":"Returns the value of a paint property in the specified style layer.\n","path":"/maplibre-gl-js-docs/api/map/#map#getpaintproperty"},{"name":"getPitch","namespace":"Map#getPitch","description":"Returns the map's current pitch (tilt).\n","path":"/maplibre-gl-js-docs/api/map/#map#getpitch"},{"name":"getPixelRatio","namespace":"Map#getPixelRatio","description":"Returns the map's pixel ratio.\n","path":"/maplibre-gl-js-docs/api/map/#map#getpixelratio"},{"name":"getRenderWorldCopies","namespace":"Map#getRenderWorldCopies","description":"Returns the state of `renderWorldCopies`. If `true`, multiple copies of the world will be rendered side by side beyond -180 and 180 degrees longitude. If set to `false`:\n\n-   When the map is zoomed out far enough that a single representation of the world does not fill the map's entire\n\ncontainer, there will be blank space beyond 180 and -180 degrees longitude.\n\n-   Features that cross 180 and -180 degrees longitude will be cut in two (with one portion on the right edge of the\n\nmap and the other on the left edge of the map) at every zoom level.\n","path":"/maplibre-gl-js-docs/api/map/#map#getrenderworldcopies"},{"name":"getSource","namespace":"Map#getSource","description":"Returns the source with the specified ID in the map's style.\n\nThis method is often used to update a source using the instance members for the relevant\nsource type as defined in [Sources](#sources).\nFor example, setting the `data` for a GeoJSON source or updating the `url` and `coordinates`\nof an image source.\n","path":"/maplibre-gl-js-docs/api/map/#map#getsource"},{"name":"getStyle","namespace":"Map#getStyle","description":"Returns the map's MapLibre style object, a JSON object which can be used to recreate the map's style.\n","path":"/maplibre-gl-js-docs/api/map/#map#getstyle"},{"name":"getZoom","namespace":"Map#getZoom","description":"Returns the map's current zoom level.\n","path":"/maplibre-gl-js-docs/api/map/#map#getzoom"},{"name":"hasControl","namespace":"Map#hasControl","description":"Checks if a control exists on the map.\n","path":"/maplibre-gl-js-docs/api/map/#map#hascontrol"},{"name":"hasImage","namespace":"Map#hasImage","description":"Check whether or not an image with a specific ID exists in the style. This checks both images\nin the style's original sprite and any images\nthat have been added at runtime using [Map#addImage](Map#addImage).\n","path":"/maplibre-gl-js-docs/api/map/#map#hasimage"},{"name":"isMoving","namespace":"Map#isMoving","description":"Returns true if the map is panning, zooming, rotating, or pitching due to a camera animation or user gesture.\n","path":"/maplibre-gl-js-docs/api/map/#map#ismoving"},{"name":"isRotating","namespace":"Map#isRotating","description":"Returns true if the map is rotating due to a camera animation or user gesture.\n","path":"/maplibre-gl-js-docs/api/map/#map#isrotating"},{"name":"isSourceLoaded","namespace":"Map#isSourceLoaded","description":"Returns a Boolean indicating whether the source is loaded. Returns `true` if the source with\nthe given ID in the map's style has no outstanding network requests, otherwise `false`.\n","path":"/maplibre-gl-js-docs/api/map/#map#issourceloaded"},{"name":"isStyleLoaded","namespace":"Map#isStyleLoaded","description":"Returns a Boolean indicating whether the map's style is fully loaded.\n","path":"/maplibre-gl-js-docs/api/map/#map#isstyleloaded"},{"name":"isZooming","namespace":"Map#isZooming","description":"Returns true if the map is zooming due to a camera animation or user gesture.\n","path":"/maplibre-gl-js-docs/api/map/#map#iszooming"},{"name":"jumpTo","namespace":"Map#jumpTo","description":"Changes any combination of center, zoom, bearing, and pitch, without\nan animated transition. The map will retain its current values for any\ndetails not specified in `options`.\n","path":"/maplibre-gl-js-docs/api/map/#map#jumpto"},{"name":"keyboard","namespace":"Map#keyboard","description":"The map's [KeyboardHandler](KeyboardHandler), which allows the user to zoom, rotate, and pan the map using keyboard\nshortcuts. Find more details and examples using `keyboard` in the [KeyboardHandler](KeyboardHandler) section.\n","path":"/maplibre-gl-js-docs/api/map/#map#keyboard"},{"name":"listImages","namespace":"Map#listImages","description":"Returns an Array of strings containing the IDs of all images currently available in the map.\nThis includes both images from the style's original sprite\nand any images that have been added at runtime using [Map#addImage](Map#addImage).\n","path":"/maplibre-gl-js-docs/api/map/#map#listimages"},{"name":"loaded","namespace":"Map#loaded","description":"Returns a Boolean indicating whether the map is fully loaded.\n\nReturns `false` if the style is not yet fully loaded,\nor if there has been a change to the sources or style that\nhas not yet fully loaded.\n","path":"/maplibre-gl-js-docs/api/map/#map#loaded"},{"name":"loadImage","namespace":"Map#loadImage","description":"Load an image from an external URL to be used with [Map#addImage](Map#addImage). External\ndomains must support [CORS](https://developer.mozilla.org/en-US/docs/Web/HTTP/Access_control_CORS).\n","path":"/maplibre-gl-js-docs/api/map/#map#loadimage"},{"name":"moveLayer","namespace":"Map#moveLayer","description":"Moves a layer to a different z-position.\n","path":"/maplibre-gl-js-docs/api/map/#map#movelayer"},{"name":"off","namespace":"Map#off","description":"Removes an event listener previously added with `Map#on`.\n","path":"/maplibre-gl-js-docs/api/map/#map#off"},{"name":"off","namespace":"Map#off","description":"Removes an event listener for layer-specific events previously added with `Map#on`.\n","path":"/maplibre-gl-js-docs/api/map/#map#off"},{"name":"on","namespace":"Map#on","description":"Adds a listener for events of a specified type, optionally limited to features in a specified style layer.\n","path":"/maplibre-gl-js-docs/api/map/#map#on"},{"name":"once","namespace":"Map#once","description":"Adds a listener that will be called only once to a specified event type.\n","path":"/maplibre-gl-js-docs/api/map/#map#once"},{"name":"once","namespace":"Map#once","description":"Adds a listener that will be called only once to a specified event type occurring on features in a specified style layer.\n","path":"/maplibre-gl-js-docs/api/map/#map#once"},{"name":"panBy","namespace":"Map#panBy","description":"Pans the map by the specified offset.\n","path":"/maplibre-gl-js-docs/api/map/#map#panby"},{"name":"panTo","namespace":"Map#panTo","description":"Pans the map to the specified location with an animated transition.\n","path":"/maplibre-gl-js-docs/api/map/#map#panto"},{"name":"project","namespace":"Map#project","description":"Returns a [Point](https://github.com/mapbox/point-geometry) representing pixel coordinates, relative to the map's `container`,\nthat correspond to the specified geographical location.\n","path":"/maplibre-gl-js-docs/api/map/#map#project"},{"name":"queryRenderedFeatures","namespace":"Map#queryRenderedFeatures","description":"Returns an array of [GeoJSON](http://geojson.org/)\n[Feature objects](https://tools.ietf.org/html/rfc7946#section-3.2)\nrepresenting visible features that satisfy the query parameters.\n","path":"/maplibre-gl-js-docs/api/map/#map#queryrenderedfeatures"},{"name":"querySourceFeatures","namespace":"Map#querySourceFeatures","description":"Returns an array of [GeoJSON](http://geojson.org/)\n[Feature objects](https://tools.ietf.org/html/rfc7946#section-3.2)\nrepresenting features within the specified vector tile or GeoJSON source that satisfy the query parameters.\n","path":"/maplibre-gl-js-docs/api/map/#map#querysourcefeatures"},{"name":"redraw","namespace":"Map#redraw","description":"Force a synchronous redraw of the map.\n","path":"/maplibre-gl-js-docs/api/map/#map#redraw"},{"name":"remove","namespace":"Map#remove","description":"Clean up and release all internal resources associated with this map.\n\nThis includes DOM elements, event bindings, web workers, and WebGL resources.\n\nUse this method when you are done using the map and wish to ensure that it no\nlonger consumes browser resources. Afterwards, you must not call any other\nmethods on the map.\n","path":"/maplibre-gl-js-docs/api/map/#map#remove"},{"name":"removeControl","namespace":"Map#removeControl","description":"Removes the control from the map.\n","path":"/maplibre-gl-js-docs/api/map/#map#removecontrol"},{"name":"removeFeatureState","namespace":"Map#removeFeatureState","description":"Removes the `state` of a feature, setting it back to the default behavior.\nIf only a `target.source` is specified, it will remove the state for all features from that source.\nIf `target.id` is also specified, it will remove all keys for that feature's state.\nIf `key` is also specified, it removes only that key from that feature's state.\nFeatures are identified by their `feature.id` attribute, which can be any number or string.\n","path":"/maplibre-gl-js-docs/api/map/#map#removefeaturestate"},{"name":"removeImage","namespace":"Map#removeImage","description":"Remove an image from a style. This can be an image from the style's original\nsprite or any images\nthat have been added at runtime using [Map#addImage](Map#addImage).\n","path":"/maplibre-gl-js-docs/api/map/#map#removeimage"},{"name":"removeLayer","namespace":"Map#removeLayer","description":"Removes the layer with the given ID from the map's style.\n\nIf no such layer exists, an `error` event is fired.\n","path":"/maplibre-gl-js-docs/api/map/#map#removelayer"},{"name":"removeSource","namespace":"Map#removeSource","description":"Removes a source from the map's style.\n","path":"/maplibre-gl-js-docs/api/map/#map#removesource"},{"name":"repaint","namespace":"Map#repaint","description":"Gets and sets a Boolean indicating whether the map will\ncontinuously repaint. This information is useful for analyzing performance.\n","path":"/maplibre-gl-js-docs/api/map/#map#repaint"},{"name":"resetNorth","namespace":"Map#resetNorth","description":"Rotates the map so that north is up (0° bearing), with an animated transition.\n","path":"/maplibre-gl-js-docs/api/map/#map#resetnorth"},{"name":"resetNorthPitch","namespace":"Map#resetNorthPitch","description":"Rotates and pitches the map so that north is up (0° bearing) and pitch is 0°, with an animated transition.\n","path":"/maplibre-gl-js-docs/api/map/#map#resetnorthpitch"},{"name":"resize","namespace":"Map#resize","description":"Resizes the map according to the dimensions of its\n`container` element.\n\nChecks if the map container size changed and updates the map if it has changed.\nThis method must be called after the map's `container` is resized programmatically\nor when the map is shown after being initially hidden with CSS.\n","path":"/maplibre-gl-js-docs/api/map/#map#resize"},{"name":"rotateTo","namespace":"Map#rotateTo","description":"Rotates the map to the specified bearing, with an animated transition. The bearing is the compass direction\nthat is \"up\"; for example, a bearing of 90° orients the map so that east is up.\n","path":"/maplibre-gl-js-docs/api/map/#map#rotateto"},{"name":"scrollZoom","namespace":"Map#scrollZoom","description":"The map's [ScrollZoomHandler](ScrollZoomHandler), which implements zooming in and out with a scroll wheel or trackpad.\nFind more details and examples using `scrollZoom` in the [ScrollZoomHandler](ScrollZoomHandler) section.\n","path":"/maplibre-gl-js-docs/api/map/#map#scrollzoom"},{"name":"setBearing","namespace":"Map#setBearing","description":"Sets the map's bearing (rotation). The bearing is the compass direction that is \"up\"; for example, a bearing\nof 90° orients the map so that east is up.\n\nEquivalent to `jumpTo({bearing: bearing})`.\n","path":"/maplibre-gl-js-docs/api/map/#map#setbearing"},{"name":"setCenter","namespace":"Map#setCenter","description":"Sets the map's geographical centerpoint. Equivalent to `jumpTo({center: center})`.\n","path":"/maplibre-gl-js-docs/api/map/#map#setcenter"},{"name":"setFeatureState","namespace":"Map#setFeatureState","description":"Sets the `state` of a feature.\nA feature's `state` is a set of user-defined key-value pairs that are assigned to a feature at runtime.\nWhen using this method, the `state` object is merged with any existing key-value pairs in the feature's state.\nFeatures are identified by their `feature.id` attribute, which can be any number or string.\n\nThis method can only be used with sources that have a `feature.id` attribute. The `feature.id` attribute can be defined in three ways:\n\n-   For vector or GeoJSON sources, including an `id` attribute in the original data file.\n-   For vector or GeoJSON sources, using the [`promoteId`](https://maplibre.org/maplibre-gl-js-docs/style-spec/sources/#vector-promoteId) option at the time the source is defined.\n-   For GeoJSON sources, using the [`generateId`](https://maplibre.org/maplibre-gl-js-docs/style-spec/sources/#geojson-generateId) option to auto-assign an `id` based on the feature's index in the source data. If you change feature data using `map.getSource('some id').setData(..)`, you may need to re-apply state taking into account updated `id` values.\n\n_Note: You can use the [`feature-state` expression](https://maplibre.org/maplibre-gl-js-docs/style-spec/expressions/#feature-state) to access the values in a feature's state object for the purposes of styling._\n","path":"/maplibre-gl-js-docs/api/map/#map#setfeaturestate"},{"name":"setFilter","namespace":"Map#setFilter","description":"Sets the filter for the specified style layer.\n\nFilters control which features a style layer renders from its source.\nAny feature for which the filter expression evaluates to `true` will be\nrendered on the map. Those that are false will be hidden.\n\nUse `setFilter` to show a subset of your source data.\n\nTo clear the filter, pass `null` or `undefined` as the second parameter.\n","path":"/maplibre-gl-js-docs/api/map/#map#setfilter"},{"name":"setLayerZoomRange","namespace":"Map#setLayerZoomRange","description":"Sets the zoom extent for the specified style layer. The zoom extent includes the\n[minimum zoom level](https://maplibre.org/maplibre-gl-js-docs/style-spec/#layer-minzoom)\nand [maximum zoom level](https://maplibre.org/maplibre-gl-js-docs/style-spec/#layer-maxzoom))\nat which the layer will be rendered.\n\nNote: For style layers using vector sources, style layers cannot be rendered at zoom levels lower than the\nminimum zoom level of the _source layer_ because the data does not exist at those zoom levels. If the minimum\nzoom level of the source layer is higher than the minimum zoom level defined in the style layer, the style\nlayer will not be rendered at all zoom levels in the zoom range.\n","path":"/maplibre-gl-js-docs/api/map/#map#setlayerzoomrange"},{"name":"setLayoutProperty","namespace":"Map#setLayoutProperty","description":"Sets the value of a layout property in the specified style layer.\n","path":"/maplibre-gl-js-docs/api/map/#map#setlayoutproperty"},{"name":"setLight","namespace":"Map#setLight","description":"Sets the any combination of light values.\n","path":"/maplibre-gl-js-docs/api/map/#map#setlight"},{"name":"setMaxBounds","namespace":"Map#setMaxBounds","description":"Sets or clears the map's geographical bounds.\n\nPan and zoom operations are constrained within these bounds.\nIf a pan or zoom is performed that would\ndisplay regions outside these bounds, the map will\ninstead display a position and zoom level\nas close as possible to the operation's request while still\nremaining within the bounds.\n","path":"/maplibre-gl-js-docs/api/map/#map#setmaxbounds"},{"name":"setMaxPitch","namespace":"Map#setMaxPitch","description":"Sets or clears the map's maximum pitch.\nIf the map's current pitch is higher than the new maximum,\nthe map will pitch to the new maximum.\n","path":"/maplibre-gl-js-docs/api/map/#map#setmaxpitch"},{"name":"setMaxZoom","namespace":"Map#setMaxZoom","description":"Sets or clears the map's maximum zoom level.\nIf the map's current zoom level is higher than the new maximum,\nthe map will zoom to the new maximum.\n","path":"/maplibre-gl-js-docs/api/map/#map#setmaxzoom"},{"name":"setMinPitch","namespace":"Map#setMinPitch","description":"Sets or clears the map's minimum pitch.\nIf the map's current pitch is lower than the new minimum,\nthe map will pitch to the new minimum.\n","path":"/maplibre-gl-js-docs/api/map/#map#setminpitch"},{"name":"setMinZoom","namespace":"Map#setMinZoom","description":"Sets or clears the map's minimum zoom level.\nIf the map's current zoom level is lower than the new minimum,\nthe map will zoom to the new minimum.\n\nIt is not always possible to zoom out and reach the set `minZoom`.\nOther factors such as map height may restrict zooming. For example,\nif the map is 512px tall it will not be possible to zoom below zoom 0\nno matter what the `minZoom` is set to.\n","path":"/maplibre-gl-js-docs/api/map/#map#setminzoom"},{"name":"setPadding","namespace":"Map#setPadding","description":"Sets the padding in pixels around the viewport.\n\nEquivalent to `jumpTo({padding: padding})`.\n","path":"/maplibre-gl-js-docs/api/map/#map#setpadding"},{"name":"setPaintProperty","namespace":"Map#setPaintProperty","description":"Sets the value of a paint property in the specified style layer.\n","path":"/maplibre-gl-js-docs/api/map/#map#setpaintproperty"},{"name":"setPitch","namespace":"Map#setPitch","description":"Sets the map's pitch (tilt). Equivalent to `jumpTo({pitch: pitch})`.\n","path":"/maplibre-gl-js-docs/api/map/#map#setpitch"},{"name":"setPixelRatio","namespace":"Map#setPixelRatio","description":"Sets the map's pixel ratio. This allows to override `devicePixelRatio`.\nAfter this call, the canvas' `width` attribute will be `container.clientWidth * pixelRatio`\nand its height attribute will be `container.clientHeight * pixelRatio`.\n","path":"/maplibre-gl-js-docs/api/map/#map#setpixelratio"},{"name":"setRenderWorldCopies","namespace":"Map#setRenderWorldCopies","description":"Sets the state of `renderWorldCopies`.\n","path":"/maplibre-gl-js-docs/api/map/#map#setrenderworldcopies"},{"name":"setStyle","namespace":"Map#setStyle","description":"Updates the map's MapLibre style object with a new value.\n\nIf a style is already set when this is used and options.diff is set to true, the map renderer will attempt to compare the given style\nagainst the map's current state and perform only the changes necessary to make the map style match the desired state. Changes in sprites\n(images used for icons and patterns) and glyphs (fonts for label text) **cannot** be diffed. If the sprites or fonts used in the current\nstyle and the given style are different in any way, the map renderer will force a full update, removing the current style and building\nthe given one from scratch.\n","path":"/maplibre-gl-js-docs/api/map/#map#setstyle"},{"name":"setTransformRequest","namespace":"Map#setTransformRequest","description":"Updates the requestManager's transform request with a new function\n","path":"/maplibre-gl-js-docs/api/map/#map#settransformrequest"},{"name":"setZoom","namespace":"Map#setZoom","description":"Sets the map's zoom level. Equivalent to `jumpTo({zoom: zoom})`.\n","path":"/maplibre-gl-js-docs/api/map/#map#setzoom"},{"name":"showCollisionBoxes","namespace":"Map#showCollisionBoxes","description":"Gets and sets a Boolean indicating whether the map will render boxes\naround all symbols in the data source, revealing which symbols\nwere rendered or which were hidden due to collisions.\nThis information is useful for debugging.\n","path":"/maplibre-gl-js-docs/api/map/#map#showcollisionboxes"},{"name":"showPadding","namespace":"Map#showPadding","description":"Gets and sets a Boolean indicating whether the map will visualize\nthe padding offsets.\n","path":"/maplibre-gl-js-docs/api/map/#map#showpadding"},{"name":"showTileBoundaries","namespace":"Map#showTileBoundaries","description":"Gets and sets a Boolean indicating whether the map will render an outline\naround each tile and the tile ID. These tile boundaries are useful for\ndebugging.\n\nThe uncompressed file size of the first vector source is drawn in the top left\ncorner of each tile, next to the tile ID.\n","path":"/maplibre-gl-js-docs/api/map/#map#showtileboundaries"},{"name":"snapToNorth","namespace":"Map#snapToNorth","description":"Snaps the map so that north is up (0° bearing), if the current bearing is close enough to it (i.e. within the\n`bearingSnap` threshold).\n","path":"/maplibre-gl-js-docs/api/map/#map#snaptonorth"},{"name":"stop","namespace":"Map#stop","description":"Stops any animated transition underway.\n","path":"/maplibre-gl-js-docs/api/map/#map#stop"},{"name":"touchPitch","namespace":"Map#touchPitch","description":"The map's [TouchPitchHandler](TouchPitchHandler), which allows the user to pitch the map with touch gestures.\nFind more details and examples using `touchPitch` in the [TouchPitchHandler](TouchPitchHandler) section.\n","path":"/maplibre-gl-js-docs/api/map/#map#touchpitch"},{"name":"touchZoomRotate","namespace":"Map#touchZoomRotate","description":"The map's [TouchZoomRotateHandler](TouchZoomRotateHandler), which allows the user to zoom or rotate the map with touch gestures.\nFind more details and examples using `touchZoomRotate` in the [TouchZoomRotateHandler](TouchZoomRotateHandler) section.\n","path":"/maplibre-gl-js-docs/api/map/#map#touchzoomrotate"},{"name":"triggerRepaint","namespace":"Map#triggerRepaint","description":"Trigger the rendering of a single frame. Use this method with custom layers to\nrepaint the map when the layer changes. Calling this multiple times before the\nnext frame is rendered will still result in only a single frame being rendered.\n","path":"/maplibre-gl-js-docs/api/map/#map#triggerrepaint"},{"name":"unproject","namespace":"Map#unproject","description":"Returns a [LngLat](LngLat) representing geographical coordinates that correspond\nto the specified pixel coordinates.\n","path":"/maplibre-gl-js-docs/api/map/#map#unproject"},{"name":"updateImage","namespace":"Map#updateImage","description":"Update an existing image in a style. This image can be displayed on the map like any other icon in the style's\nsprite using the image's ID with\n[`icon-image`](https://maplibre.org/maplibre-gl-js-docs/style-spec/#layout-symbol-icon-image),\n[`background-pattern`](https://maplibre.org/maplibre-gl-js-docs/style-spec/#paint-background-background-pattern),\n[`fill-pattern`](https://maplibre.org/maplibre-gl-js-docs/style-spec/#paint-fill-fill-pattern),\nor [`line-pattern`](https://maplibre.org/maplibre-gl-js-docs/style-spec/#paint-line-line-pattern).\n","path":"/maplibre-gl-js-docs/api/map/#map#updateimage"},{"name":"zoomIn","namespace":"Map#zoomIn","description":"Increases the map's zoom level by 1.\n","path":"/maplibre-gl-js-docs/api/map/#map#zoomin"},{"name":"zoomOut","namespace":"Map#zoomOut","description":"Decreases the map's zoom level by 1.\n","path":"/maplibre-gl-js-docs/api/map/#map#zoomout"},{"name":"zoomTo","namespace":"Map#zoomTo","description":"Zooms the map to the specified zoom level, with an animated transition.\n","path":"/maplibre-gl-js-docs/api/map/#map#zoomto"},{"name":"AttributionControl","namespace":"AttributionControl","description":"An `AttributionControl` control presents the map's attribution information.\n","path":"/maplibre-gl-js-docs/api/markers/#attributioncontrol"},{"name":"FullscreenControl","namespace":"FullscreenControl","description":"A `FullscreenControl` control contains a button for toggling the map in and out of fullscreen mode.\n","path":"/maplibre-gl-js-docs/api/markers/#fullscreencontrol"},{"name":"GeolocateControl","namespace":"GeolocateControl","description":"A `GeolocateControl` control provides a button that uses the browser's geolocation\nAPI to locate the user on the map.\n\nNot all browsers support geolocation,\nand some users may disable the feature. Geolocation support for modern\nbrowsers including Chrome requires sites to be served over HTTPS. If\ngeolocation support is not available, the GeolocateControl will show\nas disabled.\n\nThe zoom level applied will depend on the accuracy of the geolocation provided by the device.\n\nThe GeolocateControl has two modes. If `trackUserLocation` is `false` (default) the control acts as a button, which when pressed will set the map's camera to target the user location. If the user moves, the map won't update. This is most suited for the desktop. If `trackUserLocation` is `true` the control acts as a toggle button that when active the user's location is actively monitored for changes. In this mode the GeolocateControl has three interaction states:\n\n-   active - the map's camera automatically updates as the user's location changes, keeping the location dot in the center. Initial state and upon clicking the `GeolocateControl` button.\n-   passive - the user's location dot automatically updates, but the map's camera does not. Occurs upon the user initiating a map movement.\n-   disabled - occurs if Geolocation is not available, disabled or denied.\n\nThese interaction states can't be controlled programmatically, rather they are set based on user interactions.\n","path":"/maplibre-gl-js-docs/api/markers/#geolocatecontrol"},{"name":"error","namespace":"GeolocateControl.event:error","description":"Fired on each Geolocation API position update which returned as an error.\n","path":"/maplibre-gl-js-docs/api/markers/#geolocatecontrol.event:error"},{"name":"geolocate","namespace":"GeolocateControl.event:geolocate","description":"Fired on each Geolocation API position update which returned as success.\n","path":"/maplibre-gl-js-docs/api/markers/#geolocatecontrol.event:geolocate"},{"name":"outofmaxbounds","namespace":"GeolocateControl.event:outofmaxbounds","description":"Fired on each Geolocation API position update which returned as success but user position is out of map maxBounds.\n","path":"/maplibre-gl-js-docs/api/markers/#geolocatecontrol.event:outofmaxbounds"},{"name":"trackuserlocationend","namespace":"GeolocateControl.event:trackuserlocationend","description":"Fired when the Geolocate Control changes to the background state, which happens when a user changes the camera during an active position lock. This only applies when trackUserLocation is true. In the background state, the dot on the map will update with location updates but the camera will not.\n","path":"/maplibre-gl-js-docs/api/markers/#geolocatecontrol.event:trackuserlocationend"},{"name":"trackuserlocationstart","namespace":"GeolocateControl.event:trackuserlocationstart","description":"Fired when the Geolocate Control changes to the active lock state, which happens either upon first obtaining a successful Geolocation API position for the user (a geolocate event will follow), or the user clicks the geolocate button when in the background state which uses the last known position to recenter the map and enter active lock state (no geolocate event will follow unless the users's location changes).\n","path":"/maplibre-gl-js-docs/api/markers/#geolocatecontrol.event:trackuserlocationstart"},{"name":"trigger","namespace":"GeolocateControl#trigger","description":"Programmatically request and move the map to the user's location.\n","path":"/maplibre-gl-js-docs/api/markers/#geolocatecontrol#trigger"},{"name":"IControl","namespace":"IControl","description":"Interface for interactive controls added to the map. This is a\nspecification for implementers to model: it is not\nan exported method or class.\n\nControls must implement `onAdd` and `onRemove`, and must own an\nelement, which is often a `div` element. To use MapLibre GL JS's\ndefault control styling, add the `maplibregl-ctrl` class to your control's\nnode.\n","path":"/maplibre-gl-js-docs/api/markers/#icontrol"},{"name":"getDefaultPosition","namespace":"IControl#getDefaultPosition","description":"Optionally provide a default position for this control. If this method\nis implemented and [Map#addControl](Map#addControl) is called without the `position`\nparameter, the value returned by getDefaultPosition will be used as the\ncontrol's position.\n","path":"/maplibre-gl-js-docs/api/markers/#icontrol#getdefaultposition"},{"name":"onAdd","namespace":"IControl#onAdd","description":"Register a control on the map and give it a chance to register event listeners\nand resources. This method is called by [Map#addControl](Map#addControl)\ninternally.\n","path":"/maplibre-gl-js-docs/api/markers/#icontrol#onadd"},{"name":"onRemove","namespace":"IControl#onRemove","description":"Unregister a control on the map and give it a chance to detach event listeners\nand resources. This method is called by [Map#removeControl](Map#removeControl)\ninternally.\n","path":"/maplibre-gl-js-docs/api/markers/#icontrol#onremove"},{"name":"Marker","namespace":"Marker","description":"Creates a marker component\n","path":"/maplibre-gl-js-docs/api/markers/#marker"},{"name":"drag","namespace":"Marker.event:drag","description":"Fired while dragging\n","path":"/maplibre-gl-js-docs/api/markers/#marker.event:drag"},{"name":"dragend","namespace":"Marker.event:dragend","description":"Fired when the marker is finished being dragged\n","path":"/maplibre-gl-js-docs/api/markers/#marker.event:dragend"},{"name":"dragstart","namespace":"Marker.event:dragstart","description":"Fired when dragging starts\n","path":"/maplibre-gl-js-docs/api/markers/#marker.event:dragstart"},{"name":"addTo","namespace":"Marker#addTo","description":"Attaches the `Marker` to a `Map` object.\n","path":"/maplibre-gl-js-docs/api/markers/#marker#addto"},{"name":"getElement","namespace":"Marker#getElement","description":"Returns the `Marker`'s HTML element.\n","path":"/maplibre-gl-js-docs/api/markers/#marker#getelement"},{"name":"getLngLat","namespace":"Marker#getLngLat","description":"Get the marker's geographical location.\n\nThe longitude of the result may differ by a multiple of 360 degrees from the longitude previously\nset by `setLngLat` because `Marker` wraps the anchor longitude across copies of the world to keep\nthe marker on screen.\n","path":"/maplibre-gl-js-docs/api/markers/#marker#getlnglat"},{"name":"getOffset","namespace":"Marker#getOffset","description":"Get the marker's offset.\n","path":"/maplibre-gl-js-docs/api/markers/#marker#getoffset"},{"name":"getPitchAlignment","namespace":"Marker#getPitchAlignment","description":"Returns the current `pitchAlignment` property of the marker.\n","path":"/maplibre-gl-js-docs/api/markers/#marker#getpitchalignment"},{"name":"getPopup","namespace":"Marker#getPopup","description":"Returns the [Popup](Popup) instance that is bound to the [Marker](Marker).\n","path":"/maplibre-gl-js-docs/api/markers/#marker#getpopup"},{"name":"getRotation","namespace":"Marker#getRotation","description":"Returns the current rotation angle of the marker (in degrees).\n","path":"/maplibre-gl-js-docs/api/markers/#marker#getrotation"},{"name":"getRotationAlignment","namespace":"Marker#getRotationAlignment","description":"Returns the current `rotationAlignment` property of the marker.\n","path":"/maplibre-gl-js-docs/api/markers/#marker#getrotationalignment"},{"name":"isDraggable","namespace":"Marker#isDraggable","description":"Returns true if the marker can be dragged\n","path":"/maplibre-gl-js-docs/api/markers/#marker#isdraggable"},{"name":"remove","namespace":"Marker#remove","description":"Removes the marker from a map\n","path":"/maplibre-gl-js-docs/api/markers/#marker#remove"},{"name":"setDraggable","namespace":"Marker#setDraggable","description":"Sets the `draggable` property and functionality of the marker\n","path":"/maplibre-gl-js-docs/api/markers/#marker#setdraggable"},{"name":"setLngLat","namespace":"Marker#setLngLat","description":"Set the marker's geographical position and move it.\n","path":"/maplibre-gl-js-docs/api/markers/#marker#setlnglat"},{"name":"setOffset","namespace":"Marker#setOffset","description":"Sets the offset of the marker\n","path":"/maplibre-gl-js-docs/api/markers/#marker#setoffset"},{"name":"setPitchAlignment","namespace":"Marker#setPitchAlignment","description":"Sets the `pitchAlignment` property of the marker.\n","path":"/maplibre-gl-js-docs/api/markers/#marker#setpitchalignment"},{"name":"setPopup","namespace":"Marker#setPopup","description":"Binds a [Popup](Popup) to the [Marker](Marker).\n","path":"/maplibre-gl-js-docs/api/markers/#marker#setpopup"},{"name":"setRotation","namespace":"Marker#setRotation","description":"Sets the `rotation` property of the marker.\n","path":"/maplibre-gl-js-docs/api/markers/#marker#setrotation"},{"name":"setRotationAlignment","namespace":"Marker#setRotationAlignment","description":"Sets the `rotationAlignment` property of the marker.\n","path":"/maplibre-gl-js-docs/api/markers/#marker#setrotationalignment"},{"name":"togglePopup","namespace":"Marker#togglePopup","description":"Opens or closes the [Popup](Popup) instance that is bound to the [Marker](Marker), depending on the current state of the [Popup](Popup).\n","path":"/maplibre-gl-js-docs/api/markers/#marker#togglepopup"},{"name":"NavigationControl","namespace":"NavigationControl","description":"A `NavigationControl` control contains zoom buttons and a compass.\n","path":"/maplibre-gl-js-docs/api/markers/#navigationcontrol"},{"name":"Popup","namespace":"Popup","description":"A popup component.\n","path":"/maplibre-gl-js-docs/api/markers/#popup"},{"name":"close","namespace":"Popup.event:close","description":"Fired when the popup is closed manually or programatically.\n","path":"/maplibre-gl-js-docs/api/markers/#popup.event:close"},{"name":"open","namespace":"Popup.event:open","description":"Fired when the popup is opened manually or programatically.\n","path":"/maplibre-gl-js-docs/api/markers/#popup.event:open"},{"name":"addClassName","namespace":"Popup#addClassName","description":"Adds a CSS class to the popup container element.\n","path":"/maplibre-gl-js-docs/api/markers/#popup#addclassname"},{"name":"addTo","namespace":"Popup#addTo","description":"Adds the popup to a map.\n","path":"/maplibre-gl-js-docs/api/markers/#popup#addto"},{"name":"getElement","namespace":"Popup#getElement","description":"Returns the `Popup`'s HTML element.\n","path":"/maplibre-gl-js-docs/api/markers/#popup#getelement"},{"name":"getLngLat","namespace":"Popup#getLngLat","description":"Returns the geographical location of the popup's anchor.\n\nThe longitude of the result may differ by a multiple of 360 degrees from the longitude previously\nset by `setLngLat` because `Popup` wraps the anchor longitude across copies of the world to keep\nthe popup on screen.\n","path":"/maplibre-gl-js-docs/api/markers/#popup#getlnglat"},{"name":"getMaxWidth","namespace":"Popup#getMaxWidth","description":"Returns the popup's maximum width.\n","path":"/maplibre-gl-js-docs/api/markers/#popup#getmaxwidth"},{"name":"isOpen","namespace":"Popup#isOpen","description":"","path":"/maplibre-gl-js-docs/api/markers/#popup#isopen"},{"name":"remove","namespace":"Popup#remove","description":"Removes the popup from the map it has been added to.\n","path":"/maplibre-gl-js-docs/api/markers/#popup#remove"},{"name":"removeClassName","namespace":"Popup#removeClassName","description":"Removes a CSS class from the popup container element.\n","path":"/maplibre-gl-js-docs/api/markers/#popup#removeclassname"},{"name":"setDOMContent","namespace":"Popup#setDOMContent","description":"Sets the popup's content to the element provided as a DOM node.\n","path":"/maplibre-gl-js-docs/api/markers/#popup#setdomcontent"},{"name":"setHTML","namespace":"Popup#setHTML","description":"Sets the popup's content to the HTML provided as a string.\n\nThis method does not perform HTML filtering or sanitization, and must be\nused only with trusted content. Consider [Popup#setText](Popup#setText) if\nthe content is an untrusted text string.\n","path":"/maplibre-gl-js-docs/api/markers/#popup#sethtml"},{"name":"setLngLat","namespace":"Popup#setLngLat","description":"Sets the geographical location of the popup's anchor, and moves the popup to it. Replaces trackPointer() behavior.\n","path":"/maplibre-gl-js-docs/api/markers/#popup#setlnglat"},{"name":"setMaxWidth","namespace":"Popup#setMaxWidth","description":"Sets the popup's maximum width. This is setting the CSS property `max-width`.\nAvailable values can be found here: <https://developer.mozilla.org/en-US/docs/Web/CSS/max-width>\n","path":"/maplibre-gl-js-docs/api/markers/#popup#setmaxwidth"},{"name":"setOffset","namespace":"Popup#setOffset","description":"Sets the popup's offset.\n","path":"/maplibre-gl-js-docs/api/markers/#popup#setoffset"},{"name":"setText","namespace":"Popup#setText","description":"Sets the popup's content to a string of text.\n\nThis function creates a [Text](https://developer.mozilla.org/en-US/docs/Web/API/Text) node in the DOM,\nso it cannot insert raw HTML. Use this method for security against XSS\nif the popup content is user-provided.\n","path":"/maplibre-gl-js-docs/api/markers/#popup#settext"},{"name":"toggleClassName","namespace":"Popup#toggleClassName","description":"Add or remove the given CSS class on the popup container, depending on whether the container currently has that class.\n","path":"/maplibre-gl-js-docs/api/markers/#popup#toggleclassname"},{"name":"trackPointer","namespace":"Popup#trackPointer","description":"Tracks the popup anchor to the cursor position on screens with a pointer device (it will be hidden on touchscreens). Replaces the `setLngLat` behavior.\nFor most use cases, set `closeOnClick` and `closeButton` to `false`.\n","path":"/maplibre-gl-js-docs/api/markers/#popup#trackpointer"},{"name":"ScaleControl","namespace":"ScaleControl","description":"A `ScaleControl` control displays the ratio of a distance on the map to the corresponding distance on the ground.\n","path":"/maplibre-gl-js-docs/api/markers/#scalecontrol"},{"name":"setUnit","namespace":"ScaleControl#setUnit","description":"Set the scale's unit of the distance\n","path":"/maplibre-gl-js-docs/api/markers/#scalecontrol#setunit"},{"name":"addProtocol","namespace":"addProtocol","description":"Sets a custom load tile function that will be called when using a source that starts with a custom url schema.\nThe example below will be triggered for custom:// urls defined in the sources list in the style definitions.\nThe function passed will receive the request parameters and should call the callback with the resulting request,\nfor example a pbf vector tile, non-compressed, represented as ArrayBuffer.\n","path":"/maplibre-gl-js-docs/api/properties/#addprotocol"},{"name":"AnimationOptions","namespace":"AnimationOptions","description":"Options common to map movement methods that involve animation, such as [Map#panBy](Map#panBy) and\n[Map#easeTo](Map#easeTo), controlling the duration and easing function of the animation. All properties\nare optional.\n","path":"/maplibre-gl-js-docs/api/properties/#animationoptions"},{"name":"CameraOptions","namespace":"CameraOptions","description":"Options common to [Map#jumpTo](Map#jumpTo), [Map#easeTo](Map#easeTo), and [Map#flyTo](Map#flyTo), controlling the desired location,\nzoom, bearing, and pitch of the camera. All properties are optional, and when a property is omitted, the current\ncamera value for that property will remain unchanged.\n","path":"/maplibre-gl-js-docs/api/properties/#cameraoptions"},{"name":"clearPrewarmedResources","namespace":"clearPrewarmedResources","description":"Clears up resources that have previously been created by `maplibregl.prewarm()`.\nNote that this is typically not necessary. You should only call this function\nif you expect the user of your app to not return to a Map view at any point\nin your application.\n","path":"/maplibre-gl-js-docs/api/properties/#clearprewarmedresources"},{"name":"clearStorage","namespace":"clearStorage","description":"Clears browser storage used by this library. Using this method flushes the MapLibre tile\ncache that is managed by this library. Tiles may still be cached by the browser\nin some cases.\n\nThis API is supported on browsers where the [`Cache` API](https://developer.mozilla.org/en-US/docs/Web/API/Cache)\nis supported and enabled. This includes all major browsers when pages are served over\n`https://`, except Internet Explorer and Edge Mobile.\n\nWhen called in unsupported browsers or environments (private or incognito mode), the\ncallback will be called with an error argument.\n","path":"/maplibre-gl-js-docs/api/properties/#clearstorage"},{"name":"CustomLayerInterface","namespace":"CustomLayerInterface","description":"Interface for custom style layers. This is a specification for\nimplementers to model: it is not an exported method or class.\n\nCustom layers allow a user to render directly into the map's GL context using the map's camera.\nThese layers can be added between any regular layers using [Map#addLayer](Map#addLayer).\n\nCustom layers must have a unique `id` and must have the `type` of `\"custom\"`.\nThey must implement `render` and may implement `prerender`, `onAdd` and `onRemove`.\nThey can trigger rendering using [Map#triggerRepaint](Map#triggerRepaint)\nand they should appropriately handle <Map.event:webglcontextlost> and\n<Map.event:webglcontextrestored>.\n\nThe `renderingMode` property controls whether the layer is treated as a `\"2d\"` or `\"3d\"` map layer. Use:\n\n-   `\"renderingMode\": \"3d\"` to use the depth buffer and share it with other layers\n-   `\"renderingMode\": \"2d\"` to add a layer with no depth. If you need to use the depth buffer for a `\"2d\"` layer you must use an offscreen\n    framebuffer and [CustomLayerInterface#prerender](CustomLayerInterface#prerender)\n","path":"/maplibre-gl-js-docs/api/properties/#customlayerinterface"},{"name":"id","namespace":"CustomLayerInterface#id","description":"","path":"/maplibre-gl-js-docs/api/properties/#customlayerinterface#id"},{"name":"onAdd","namespace":"CustomLayerInterface#onAdd","description":"Optional method called when the layer has been added to the Map with [Map#addLayer](Map#addLayer). This\ngives the layer a chance to initialize gl resources and register event listeners.\n","path":"/maplibre-gl-js-docs/api/properties/#customlayerinterface#onadd"},{"name":"onRemove","namespace":"CustomLayerInterface#onRemove","description":"Optional method called when the layer has been removed from the Map with [Map#removeLayer](Map#removeLayer). This\ngives the layer a chance to clean up gl resources and event listeners.\n","path":"/maplibre-gl-js-docs/api/properties/#customlayerinterface#onremove"},{"name":"prerender","namespace":"CustomLayerInterface#prerender","description":"Optional method called during a render frame to allow a layer to prepare resources or render into a texture.\n\nThe layer cannot make any assumptions about the current GL state and must bind a framebuffer before rendering.\n","path":"/maplibre-gl-js-docs/api/properties/#customlayerinterface#prerender"},{"name":"render","namespace":"CustomLayerInterface#render","description":"Called during a render frame allowing the layer to draw into the GL context.\n\nThe layer can assume blending and depth state is set to allow the layer to properly\nblend and clip other layers. The layer cannot make any other assumptions about the\ncurrent GL state.\n\nIf the layer needs to render to a texture, it should implement the `prerender` method\nto do this and only use the `render` method for drawing directly into the main framebuffer.\n\nThe blend function is set to `gl.blendFunc(gl.ONE, gl.ONE_MINUS_SRC_ALPHA)`. This expects\ncolors to be provided in premultiplied alpha form where the `r`, `g` and `b` values are already\nmultiplied by the `a` value. If you are unable to provide colors in premultiplied form you\nmay want to change the blend function to\n`gl.blendFuncSeparate(gl.SRC_ALPHA, gl.ONE_MINUS_SRC_ALPHA, gl.ONE, gl.ONE_MINUS_SRC_ALPHA)`.\n","path":"/maplibre-gl-js-docs/api/properties/#customlayerinterface#render"},{"name":"renderingMode","namespace":"CustomLayerInterface#renderingMode","description":"","path":"/maplibre-gl-js-docs/api/properties/#customlayerinterface#renderingmode"},{"name":"type","namespace":"CustomLayerInterface#type","description":"","path":"/maplibre-gl-js-docs/api/properties/#customlayerinterface#type"},{"name":"getRTLTextPluginStatus","namespace":"getRTLTextPluginStatus","description":"Gets the map's [RTL text plugin](https://www.mapbox.com/mapbox-gl-js/plugins/#mapbox-gl-rtl-text) status.\nThe status can be `unavailable` (i.e. not requested or removed), `loading`, `loaded` or `error`.\nIf the status is `loaded` and the plugin is requested again, an error will be thrown.\n","path":"/maplibre-gl-js-docs/api/properties/#getrtltextpluginstatus"},{"name":"maxParallelImageRequests","namespace":"maxParallelImageRequests","description":"Gets and sets the maximum number of images (raster tiles, sprites, icons) to load in parallel,\nwhich affects performance in raster-heavy maps. 16 by default.\n","path":"/maplibre-gl-js-docs/api/properties/#maxparallelimagerequests"},{"name":"PaddingOptions","namespace":"PaddingOptions","description":"Options for setting padding on calls to methods such as [Map#fitBounds](Map#fitBounds), [Map#fitScreenCoordinates](Map#fitScreenCoordinates), and [Map#setPadding](Map#setPadding). Adjust these options to set the amount of padding in pixels added to the edges of the canvas. Set a uniform padding on all edges or individual values for each edge. All properties of this object must be\nnon-negative integers.\n","path":"/maplibre-gl-js-docs/api/properties/#paddingoptions"},{"name":"bottom","namespace":"PaddingOptions.bottom","description":"","path":"/maplibre-gl-js-docs/api/properties/#paddingoptions.bottom"},{"name":"left","namespace":"PaddingOptions.left","description":"","path":"/maplibre-gl-js-docs/api/properties/#paddingoptions.left"},{"name":"right","namespace":"PaddingOptions.right","description":"","path":"/maplibre-gl-js-docs/api/properties/#paddingoptions.right"},{"name":"top","namespace":"PaddingOptions.top","description":"","path":"/maplibre-gl-js-docs/api/properties/#paddingoptions.top"},{"name":"prewarm","namespace":"prewarm","description":"Initializes resources like WebWorkers that can be shared across maps to lower load\ntimes in some situations. `maplibregl.workerUrl` and `maplibregl.workerCount`, if being\nused, must be set before `prewarm()` is called to have an effect.\n\nBy default, the lifecycle of these resources is managed automatically, and they are\nlazily initialized when a Map is first created. By invoking `prewarm()`, these\nresources will be created ahead of time, and will not be cleared when the last Map\nis removed from the page. This allows them to be re-used by new Map instances that\nare created later. They can be manually cleared by calling\n`maplibregl.clearPrewarmedResources()`. This is only necessary if your web page remains\nactive but stops using maps altogether.\n\nThis is primarily useful when using GL-JS maps in a single page app, wherein a user\nwould navigate between various views that can cause Map instances to constantly be\ncreated and destroyed.\n","path":"/maplibre-gl-js-docs/api/properties/#prewarm"},{"name":"removeProtocol","namespace":"removeProtocol","description":"Removes a previusly added protocol\n","path":"/maplibre-gl-js-docs/api/properties/#removeprotocol"},{"name":"RequestParameters","namespace":"RequestParameters","description":"A `RequestParameters` object to be returned from Map.options.transformRequest callbacks.\n","path":"/maplibre-gl-js-docs/api/properties/#requestparameters"},{"name":"setRTLTextPlugin","namespace":"setRTLTextPlugin","description":"Sets the map's [RTL text plugin](https://www.mapbox.com/mapbox-gl-js/plugins/#mapbox-gl-rtl-text).\nNecessary for supporting the Arabic and Hebrew languages, which are written right-to-left.\n","path":"/maplibre-gl-js-docs/api/properties/#setrtltextplugin"},{"name":"StyleImageInterface","namespace":"StyleImageInterface","description":"Interface for dynamically generated style images. This is a specification for\nimplementers to model: it is not an exported method or class.\n\nImages implementing this interface can be redrawn for every frame. They can be used to animate\nicons and patterns or make them respond to user input. Style images can implement a\n[StyleImageInterface#render](StyleImageInterface#render) method. The method is called every frame and\ncan be used to update the image.\n","path":"/maplibre-gl-js-docs/api/properties/#styleimageinterface"},{"name":"data","namespace":"StyleImageInterface#data","description":"","path":"/maplibre-gl-js-docs/api/properties/#styleimageinterface#data"},{"name":"height","namespace":"StyleImageInterface#height","description":"","path":"/maplibre-gl-js-docs/api/properties/#styleimageinterface#height"},{"name":"onAdd","namespace":"StyleImageInterface#onAdd","description":"Optional method called when the layer has been added to the Map with [Map#addImage](Map#addImage).\n","path":"/maplibre-gl-js-docs/api/properties/#styleimageinterface#onadd"},{"name":"onRemove","namespace":"StyleImageInterface#onRemove","description":"Optional method called when the icon is removed from the map with [Map#removeImage](Map#removeImage).\nThis gives the image a chance to clean up resources and event listeners.\n","path":"/maplibre-gl-js-docs/api/properties/#styleimageinterface#onremove"},{"name":"render","namespace":"StyleImageInterface#render","description":"This method is called once before every frame where the icon will be used.\nThe method can optionally update the image's `data` member with a new image.\n\nIf the method updates the image it must return `true` to commit the change.\nIf the method returns `false` or nothing the image is assumed to not have changed.\n\nIf updates are infrequent it maybe easier to use [Map#updateImage](Map#updateImage) to update\nthe image instead of implementing this method.\n","path":"/maplibre-gl-js-docs/api/properties/#styleimageinterface#render"},{"name":"width","namespace":"StyleImageInterface#width","description":"","path":"/maplibre-gl-js-docs/api/properties/#styleimageinterface#width"},{"name":"supported","namespace":"supported","description":"Test whether the browser supports MapLibre GL JS.\n","path":"/maplibre-gl-js-docs/api/properties/#supported"},{"name":"workerCount","namespace":"workerCount","description":"Gets and sets the number of web workers instantiated on a page with GL JS maps.\nBy default, it is set to half the number of CPU cores (capped at 6).\nMake sure to set this property before creating any map instances for it to have effect.\n","path":"/maplibre-gl-js-docs/api/properties/#workercount"},{"name":"CanvasSource","namespace":"CanvasSource","description":"A data source containing the contents of an HTML canvas. See [CanvasSourceOptions](CanvasSourceOptions) for detailed documentation of options.\n","path":"/maplibre-gl-js-docs/api/sources/#canvassource"},{"name":"getCanvas","namespace":"CanvasSource#getCanvas","description":"Returns the HTML `canvas` element.\n","path":"/maplibre-gl-js-docs/api/sources/#canvassource#getcanvas"},{"name":"pause","namespace":"CanvasSource#pause","description":"Disables animation. The map will display a static copy of the canvas image.\n","path":"/maplibre-gl-js-docs/api/sources/#canvassource#pause"},{"name":"play","namespace":"CanvasSource#play","description":"Enables animation. The image will be copied from the canvas to the map on each frame.\n","path":"/maplibre-gl-js-docs/api/sources/#canvassource#play"},{"name":"CanvasSourceOptions","namespace":"CanvasSourceOptions","description":"Options to add a canvas source type to the map.\n","path":"/maplibre-gl-js-docs/api/sources/#canvassourceoptions"},{"name":"GeoJSONSource","namespace":"GeoJSONSource","description":"A source containing GeoJSON.\n(See the [Style Specification](https://maplibre.org/maplibre-gl-js-docs/style-spec/#sources-geojson) for detailed documentation of options.)\n","path":"/maplibre-gl-js-docs/api/sources/#geojsonsource"},{"name":"getClusterChildren","namespace":"GeoJSONSource#getClusterChildren","description":"For clustered sources, fetches the children of the given cluster on the next zoom level (as an array of GeoJSON features).\n","path":"/maplibre-gl-js-docs/api/sources/#geojsonsource#getclusterchildren"},{"name":"getClusterExpansionZoom","namespace":"GeoJSONSource#getClusterExpansionZoom","description":"For clustered sources, fetches the zoom at which the given cluster expands.\n","path":"/maplibre-gl-js-docs/api/sources/#geojsonsource#getclusterexpansionzoom"},{"name":"getClusterLeaves","namespace":"GeoJSONSource#getClusterLeaves","description":"For clustered sources, fetches the original points that belong to the cluster (as an array of GeoJSON features).\n","path":"/maplibre-gl-js-docs/api/sources/#geojsonsource#getclusterleaves"},{"name":"setData","namespace":"GeoJSONSource#setData","description":"Sets the GeoJSON data and re-renders the map.\n","path":"/maplibre-gl-js-docs/api/sources/#geojsonsource#setdata"},{"name":"ImageSource","namespace":"ImageSource","description":"A data source containing an image.\n(See the [Style Specification](https://maplibre.org/maplibre-gl-js-docs/style-spec/#sources-image) for detailed documentation of options.)\n","path":"/maplibre-gl-js-docs/api/sources/#imagesource"},{"name":"setCoordinates","namespace":"ImageSource#setCoordinates","description":"Sets the image's coordinates and re-renders the map.\n","path":"/maplibre-gl-js-docs/api/sources/#imagesource#setcoordinates"},{"name":"updateImage","namespace":"ImageSource#updateImage","description":"Updates the image URL and, optionally, the coordinates. To avoid having the image flash after changing,\nset the `raster-fade-duration` paint property on the raster layer to 0.\n","path":"/maplibre-gl-js-docs/api/sources/#imagesource#updateimage"},{"name":"VideoSource","namespace":"VideoSource","description":"A data source containing video.\n(See the [Style Specification](https://maplibre.org/maplibre-gl-js-docs/style-spec/#sources-video) for detailed documentation of options.)\n","path":"/maplibre-gl-js-docs/api/sources/#videosource"},{"name":"getVideo","namespace":"VideoSource#getVideo","description":"Returns the HTML `video` element.\n","path":"/maplibre-gl-js-docs/api/sources/#videosource#getvideo"},{"name":"pause","namespace":"VideoSource#pause","description":"Pauses the video.\n","path":"/maplibre-gl-js-docs/api/sources/#videosource#pause"},{"name":"play","namespace":"VideoSource#play","description":"Plays the video.\n","path":"/maplibre-gl-js-docs/api/sources/#videosource#play"},{"name":"setCoordinates","namespace":"VideoSource#setCoordinates","description":"Sets the video's coordinates and re-renders the map.\n","path":"/maplibre-gl-js-docs/api/sources/#videosource#setcoordinates"},{"name":"setTiles","namespace":"VectorTileSource#setTiles","description":"Sets the source `tiles` property and re-renders the map.\n","path":"#vectortilesource#settiles"},{"name":"setUrl","namespace":"VectorTileSource#setUrl","description":"Sets the source `url` property and re-renders the map.\n","path":"#vectortilesource#seturl"}];